<!-- 耐药性基因 | created by JanvierCheng -->
<template>
  <div id="sampleSiteInfoDiv">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="检出率分析"></my-location>
              <my-navigat activ="2-1"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="70px" :inline="true" class="demo-form-inline">
                      <el-form-item label="样品编号" prop="id">
                          <el-input v-model="formSamInfo.id"></el-input>
                      </el-form-item>
                      <el-form-item label="采样地名" prop="caiyangdi">
                          <el-input v-model="formSamInfo.caiyangdi"></el-input>
                      </el-form-item>
                      <el-form-item label="养殖场名" prop="yangzhi">
                          <el-input v-model="formSamInfo.yangzhi"></el-input>
                      </el-form-item>
                      <el-form-item label="日龄" prop="age">
                          <el-input v-model="formSamInfo.age"></el-input>
                      </el-form-item>
                      <el-form-item label="省份" prop="province">
                          <el-select  v-model="formSamInfo.province" multiple placeholder="选择省份">
                              <el-option :value="prvnc.name" v-for="prvnc in prvncs"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="菌属" prop="junshu">
                          <el-select  v-model="formSamInfo.junshu" multiple placeholder="选择菌属">
                              <el-option :value="item" v-for="item in junshuSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="动物" prop="animal">
                          <el-select  v-model="formSamInfo.animal" multiple placeholder="选择动物">
                              <el-option :value="item.animalname" v-for="item in animalSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="部位" prop="part">
                          <el-select  v-model="formSamInfo.part" multiple placeholder="选择部位">
                              <el-option :value="item.partname" v-for="item in partSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="来源" prop="source">
                          <el-select  v-model="formSamInfo.source" multiple placeholder="选择来源">
                              <el-option :value="item.sourcename" v-for="item in sourceSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="采样时间">
                          <el-date-picker type="date" placeholder="选择日期" v-model="formSamInfo.date1" :editable="false" style="width: 144px !important;"></el-date-picker>
                          -&nbsp;<el-date-picker type="date" placeholder="选择日期" v-model="formSamInfo.date2" :editable="false" style="width: 144px !important;"></el-date-picker>
                      </el-form-item>
                  </el-form>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button type="primary" icon="d-caret">导出数据</el-button>
                    <el-button type="primary" icon="more" @click="toAnalyse">分析界面</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button icon="search" @click="getList">搜索</el-button>
                    <el-button icon="close" @click="resetForm('formSamInfo')">重置</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height = "600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35" >
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      label="样品编号"
                      fixed
                      width="106">
                    </el-table-column>
                    <el-table-column
                      prop="TB_strainno"
                      label="菌株编号"
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      label="内部编号"
                      width="160">
                    </el-table-column>
                    <el-table-column
                      prop="TB_caiyangtime"
                      label="采样时间"
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_province"
                      label="采样省份"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_caiyangdi"
                      label="采样地名"
                      width="240">
                    </el-table-column>
                    <el-table-column
                      prop="TB_yangzhi"
                      label="养殖场名"
                      width="200">
                    </el-table-column>
                    <el-table-column
                      prop="TB_animal"
                      label="动物"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_age"
                      label="日龄"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_source"
                      label="来源"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_part"
                      label="部位"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_caiyangman"
                      label="采样人员"
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_history"
                      label="动物用药史"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_yangpinremark"
                      label="样品备注"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_junshu"
                      label="菌属"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_junzhong"
                      label="菌种"
                      width="130">
                    </el-table-column>
                    <el-table-column
                      prop="TB_savetime"
                      label="保存时间"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_xueqing"
                      label="血清型"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_mlst"
                      label="MLST分型"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_plg"
                      label="PLG分型"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_identifyman"
                      label="菌种鉴定人"
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_analyseman"
                      label="细菌分型人"
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_strainremark"
                      label="菌株基本信息备注"
                      width="260">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <!-- <el-dialog title="采样地信息" v-model="dialogFormVisible">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="菌株编号" :label-width="formLabelWidth" prop="id">
                      <el-input v-model="formAdd.id" auto-complete="off" :disabled="!currentOper"></el-input>
                    </el-form-item>
                    <el-form-item label="内部编号" :label-width="formLabelWidth" prop="inno">
                      <el-input v-model="formAdd.inno" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="耐药基因" :label-width="formLabelWidth" prop="gene">
                      <el-input v-model="formAdd.gene" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="插入元件" :label-width="formLabelWidth" prop="charu">
                      <el-input v-model="formAdd.charu" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="复制子" :label-width="formLabelWidth" prop="copy">
                      <el-input v-model="formAdd.copy" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="结合子" :label-width="formLabelWidth" prop="jiehe">
                      <el-input v-model="formAdd.jiehe" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="操作人" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog> -->
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import dataSet from './../publicAPI/dataSet'
import axios from 'axios'
import dataRules from './../publicAPI/dataRules'
import config from './../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data(){
      return{
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          formSamInfo: {
              id: "",
              province: [],
              caiyangdi: "",
              yangzhi: "",
              junshu: [],
              animal: [],
              part: [],
              source: [],
              age: "",
              date1: "",
              date2: "",
          },
          switchSH: false,   //查询
          switchNV: true,   //导航
          cotDivWidth:{
              width: '1150px',
          },
          totalPage: 0, //总页数
          pageSize: 100, //每页记录数
          loading2: true,    //表格加载中
          dialogFormVisible: false,
          formLabelWidth: '85px',
          currentPage: 1,   //当前页码
          currentOper: true,  //添加是true,修改是false
          prvncs: dataSet.province,
          partSelect: [],
          animalSelect: [],
          sourceSelect: [],
          junshuSelect: [],
      }
  },
  methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        toAnalyse(){
            let that = this
            localStorage.setItem('detectionRate', JSON.stringify(that.formSamInfo));
            this.$router.push({path: '/analyseDetec'})
        },
        getSelect(){
            let that = this
            axios.all([axios.get(that.URL + '/animal/selection'),
                       axios.get(that.URL + '/source/selection'),
                       axios.get(that.URL + '/part/selection'),
                       axios.post(that.URL + '/strainToNotes/category/selection')])
                .then(axios.spread((animalres, sourceres, partres, junshures) => {
                    if(animalres.data.state){
                        that.animalSelect = animalres.data.value
                    }
                    if(sourceres.data.state){
                        that.sourceSelect = sourceres.data.value
                    }
                    for(var item of partres.data.value){
                        if(item.partname == ""){
                            that.partSelect.push({
                                partno: item.partno,
                                partname: " "
                            })
                        }else{
                            that.partSelect.push({
                                partno: item.partno,
                                partname: item.partname
                            })
                        }
                    }
                    if(junshures.data.state){
                        that.junshuSelect = junshures.data.value
                    }
                }))
        },
        getList(){
            let that = this
            var param = {
                sampleno: that.formSamInfo.id,
                province: that.formSamInfo.province,    //[省份]
                farmname: that.formSamInfo.caiyangdi,
                farmaddr: that.formSamInfo.yangzhi,
                category: that.formSamInfo.junshu,  //[菌属]
                animalName: that.formSamInfo.animal, //[动物 - 编号]
                partName: that.formSamInfo.part,  //[部位 - 编号]
                source: that.formSamInfo.source,    //[来源]
                animalAge: that.formSamInfo.age,
                beginDate: that.formSamInfo.date1,
                endDate: that.formSamInfo.date2,
                page: that.currentPage,
                rows: that.pageSize,
            }
            // console.warn(JSON.stringify(param))
            this.loading2 = true
            axios.post( that.URL + '/straincheck/list', param )
                .then((res) => {
                    that.tableData = [];
                    that.totalPage = res.data.value.pages
                    for( var item of res.data.value.data){
                        that.tableData.push({
                            TB_id: item.sampleno,
                            TB_strainno: item.strainno,
                            TB_inno: item.strainalias,
                            TB_caiyangtime: item.sampledate,
                            TB_province: item.sampleprovince,
                            TB_caiyangdi: item.farmname,
                            TB_yangzhi: item.samplefarmaddr,
                            TB_animal: item.animalname,
                            TB_animal_id: item.animalno,
                            TB_age: item.sampleanimalage,
                            TB_source: item.samplesource,
                            TB_part: item.samplecollectionpart,
                            TB_part_id: item.samplecollectionpartno,
                            TB_caiyangman: item.samplecollector,
                            TB_history: item.samplemedicalhistory,
                            TB_yangpinremark: item.sampleremarks,
                            TB_junshu: item.straincategory,
                            TB_junzhong: item.straintype,
                            TB_savetime: item.strainstoragedate,
                            TB_xueqing: item.serotype,
                            TB_mlst: item.strainmlst,
                            TB_plg: item.strainplg,
                            TB_identifyman: item.strainoperator,
                            TB_analyseman: item.strainparter,
                            TB_strainremark: item.strainremarks,
                        })
                    }
                    that.loading2 = false;
                })
                .catch((error) => {
                    console.error(error);
                })
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            if(formName == 'formSamInfo'){
                this.getList();
            }
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
     },
  },
  created(){
      this.getSelect();
      this.getList();
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
/*表单输入组*/
.inputGp{
    margin-bottom: 20px;
}
.inputGp .el-form-item{
    margin-bottom: 6px;
}
.inputGp .el-input, .inputGp .el-select{
    width: 260px;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



