<template>
  <div id="samImpotDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="样品信息"></my-location>
              <my-navigat activ="1-2"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-upload
                    :action="uploadURL"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :on-success="succeedUpload"
                    :on-error="failUpload"
                    :default-file-list="fileList">
                    <el-button size="small" type="primary">点击上传</el-button>
                    <div class="el-upload__tip" slot="tip">只能上传excel文件，excel的格式模板可点击下方按钮下载</div>
                  </el-upload>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button icon="d-arrow-left" @click="goBack">返回</el-button>
                    <el-button icon="caret-bottom" type="primary" @click="downLoadFile">下载导入模板</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button type="primary" icon="edit" @click="onEdit" style="border-radius:4px;">修改</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height="600"
                    @select="deleteRow"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      fixed
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_time"
                      label="采样时间"
                      width="90">
                    </el-table-column>
                    <el-table-column
                      prop="TB_provc"
                      label="采样省份"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_site"
                      label="采样地 "
                      width="248">
                    </el-table-column>
                    <el-table-column
                      prop="TB_yname"
                      label="养殖场"
                      width="260">
                    </el-table-column>
                    <el-table-column
                      prop="TB_animal"
                      label="动物"
                      width="65">
                    </el-table-column>
                    <el-table-column
                      prop="TB_age"
                      label="日龄"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_source"
                      label="来源"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_part"
                      label="部位"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_man"
                      label="采样人"
                      width="140">
                    </el-table-column>
                    <el-table-column
                      prop="TB_history"
                      label="动物用药史"
                      width="260">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      width="120"
                      label="备注">
                    </el-table-column>
                    <el-table-column
                      prop="TB_failReason"
                      fixed="right"
                      label="失败原因"
                      width="400">
                    </el-table-column>
                  </el-table>
              </div>
              <el-dialog title="样品信息" v-model="dialogFormVisible" >
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="采样时间" :label-width="formLabelWidth" prop="time" >
                      <el-date-picker type="date" placeholder="选择日期" v-model="formAdd.time" :editable="false" ></el-date-picker>
                    </el-form-item>
                    <el-form-item label="采样省份" :label-width="formLabelWidth" prop="province">
                      <el-select  v-model="formAdd.province" placeholder="选择省份">
                          <el-option :value="prvnc.name" v-for="prvnc in prvncs"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="动物" :label-width="formLabelWidth" prop="animal">
                      <el-select  v-model="formAdd.animal"  placeholder="选择动物" >
                          <el-option :value="animal.animalno" :label="animal.animalname" v-for="animal in animalSelect"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="来源" :label-width="formLabelWidth" prop="source">
                      <el-select  v-model="formAdd.source" placeholder="选择来源">
                          <el-option :value="source.sourcename" v-for="source in sourSelect"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="部位" :label-width="formLabelWidth" prop="part">
                      <el-select  v-model="formAdd.part" placeholder="选择部位">
                          <el-option :value="part.partno" :label="part.partname" v-for="part in partSelect"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="采样地" :label-width="formLabelWidth" prop="clocate">
                      <el-select  v-model="formAdd.clocate" placeholder="选择采样地" :disabled="formAdd.province==''">
                          <el-option :value="citem" v-for="citem in cLocateSelect"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="养殖场" :label-width="formLabelWidth" prop="yazhic">
                      <el-input v-model="formAdd.yazhic" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="日龄" :label-width="formLabelWidth" prop="age">
                      <el-input v-model="formAdd.age" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="采样人" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off" ></el-input>
                    </el-form-item>
                    <el-form-item label="动物用药史" :label-width="formLabelWidth" prop="history">
                      <el-input v-model="formAdd.history" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../../components/header'
import myLocation from './../../components/location'
import myNavigat from './../../components/navigat'
import axios from 'axios'
import dataSet from './../../publicAPI/dataSet'
import dataRules from './../../publicAPI/dataRules'
import config from './../../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data() {
      return {
          URL: config.rootURL,
          uploadURL: config.rootURL + '/sample/upload',
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          formAdd: {
              time: '',
              province: '',
              clocate:'',
              yazhic: '',
              animal: '',
              age: '',
              source: '',
              part: '',
              man: '',
              history: '',
              remark: '',
              animal_id: '',
              part_id: '',
          },
          formAddRules: {
              time: [
                  { type: 'date' ,required: true, message: '请输入采样时间', trigger: 'change' },
              ],
              province: [
                  { required: true, message: '请选择省份', trigger: 'change' },
              ],
              clocate: [
                  { required: true, message: '请输入采样地', trigger: 'blur' },
              ],
              animal: [
                  { type: 'number', required: true, message: '请选择动物', trigger: 'change' },
              ],
              source: [
                  { required: true, message: '请选择来源', trigger: 'change' },
              ],
              part: [
                  { required: true, message: '请选择部位', trigger: 'change' },
              ],
          },
          switchSH: true,   //查询
          switchNV: true,   //导航
          loading2: false,
          formLabelWidth: '90px',
          cotDivWidth:{
              width: '1150px',
          },
          prvncs: dataSet.province,
          partSelect: [],
          animalSelect: [],
          sourSelect: [],
          cLocateSelect: [],
          fileList: [
          ],
          selectRow: '',
      };
  },
  methods:{
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        deleteRow(selection, row){
            this.selectRow = row.TB_id
        },
        onEdit(){
            if(this.multipleSelection.length==1){
                this.dialogFormVisible = true
                this.formAdd = {
                    time: new Date(this.multipleSelection[0].TB_time),
                    province: this.multipleSelection[0].TB_provc,
                    clocate:this.multipleSelection[0].TB_site,
                    yazhic: this.multipleSelection[0].TB_yname,
                    animal: this.multipleSelection[0].TB_animal_id,
                    age: this.multipleSelection[0].TB_age,
                    source: this.multipleSelection[0].TB_source,
                    part: this.multipleSelection[0].TB_part_id,
                    man: this.multipleSelection[0].TB_man,
                    history: this.multipleSelection[0].TB_history,
                    remark: this.multipleSelection[0].TB_history,
                }

            }
        },
        onConfirm(){
            let that = this
            this.$refs['formAdd'].validate((valid) => {
                  if (valid) {
                      var param = {
                          sampledateStr: dataRules.formatDate(that.formAdd.time),
                          sampleprovince: this.formAdd.province,
                          farmname: this.formAdd.clocate,
                          samplefarmaddr: this.formAdd.yazhic,
                          animalno: this.formAdd.animal,
                          sampleanimalage: this.formAdd.age,
                          samplesource: this.formAdd.source,
                          samplecollectionpart: this.formAdd.part,
                          samplecollector: this.formAdd.man,
                          samplemedicalhistory: this.formAdd.history,
                          sampleremarks: this.formAdd.remark,
                      }
                      var str = JSON.stringify(param)
                      axios.post(that.URL+'/sample/addition', param)
                      .then(function (res) {
                          if(res.data.state){
                              that.$message({
                                type: 'success',
                                message: res.data.value
                              });
                              that.tableData.forEach((item, index) => {
                                //   console.log(item.TB_id+' --- '+that.selectRow)
                                  if(item.TB_id == that.selectRow){
                                      that.tableData.splice(index, 1);
                                  }
                              });
                          }else{
                              that.$message({
                                type: 'error',
                                message: res.data.value
                              });
                          }
                      })
                      .catch(function (error) {
                          console.error(error);
                      });
                      that.dialogFormVisible = false
                  } else {
                    console.log('error submit!!');
                    return false;
                  }
            });
        },
        resetForm(formName){
            this.$refs[formName].resetFields();
            this.formAdd = {
                time: '',
                province: '',
                clocate:'',
                yazhic: '',
                animal: '',
                age: '',
                source: '',
                part: '',
                man: '',
                history: '',
                remark: '',
                animal_id: '',
                part_id: '',
            }
            this.dialogFormVisible = false
        },
        succeedUpload(response, file, fileList){
            let that = this;
            if(response.state){
                this.$message({
                  message: response.value,
                  duration: 5000,
                  type: 'success'
                });

            }else{
                if(typeof(response.value) != 'string'){
                    this.$message({
                      message: '成功导入【' + response.value.success + '】条，有【' + response.value.error + '】条导入失败！',
                      duration: 5000,
                      type: 'warning'
                    });
                    /*插入数据*/
                    that.tableData = [];
                    (response.value.data).forEach( ( item, index) => {
                        that.tableData.push({
                            TB_id: index,
                            TB_time: item.sampledateStr,
                            TB_provc: item.sampleprovince,
                            TB_site: item.farmname,
                            TB_yname: item.samplefarmaddr,
                            TB_animal: item.animalName,
                            TB_animal_id: item.animalno,
                            TB_age: item.sampleanimalage,
                            TB_source: item.samplesource,
                            TB_part: item.partName,
                            TB_part_id: item.samplecollectionpart,
                            TB_man: item.samplecollector,
                            TB_history: item.samplemedicalhistory,
                            TB_remark: item.sampleremarks,
                            TB_failReason: item.otherMsg
                        })
                    });
                }else{
                    this.fileList = [];
                    this.$message({
                      message: response.value,
                      type: 'error'
                    });
                }
            }
        },
        failUpload(err, response, file){
            this.fileList = [];
            this.$message({
              message: err,
              type: 'error'
            });
        },
        goBack(){
            this.$router.push({ path: '/sampleInfo' })
        },
        downLoadFile(){
            window.open('/static/uploads/sample.xls')
        },
        getSelect(){    //获取下拉框
            let that = this
            axios.all([axios.get(that.URL + '/part/selection'), axios.get(that.URL + '/animal/selection'), axios.get(that.URL + '/source/selection')])
                .then(axios.spread(function (respart, resanimal, ressource) {
                    if(respart.data.state){
                        for(var item of respart.data.value){
                            if(item.partname == ""){
                                that.partSelect.push({
                                    partno: item.partno,
                                    partname: " "
                                })
                            }else{
                                that.partSelect.push({
                                    partno: item.partno,
                                    partname: item.partname
                                })
                            }
                        }
                    }
                    if(resanimal.data.state){
                        that.animalSelect = resanimal.data.value
                    }
                    if(ressource.data.state){
                        that.sourSelect = ressource.data.value
                    }
                }))
        },
        getCLocateList(){   //对话框中的采样地下拉
            let that = this

            // console.warn(typeof(that.formAdd.province))
            axios.post(that.URL + '/farm/selection', {
                    farmprovince:that.formAdd.province
                })
                .then((res) => {
                    that.cLocateSelect = []
                    that.cLocateSelect = res.data.value
                })
                .catch((error) => {
                    console.error(error);
                })
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
   },
   watch:{
       switchNV(val){
           if(val){
              this.cotDivWidth = {
                   width: '1150px',
              }
          }else{
              this.cotDivWidth = {
                   width: '100%',
              }
          }
      },
      'formAdd.province': function(_new,_old){
        //   console.log(_old+'----'+_new)
            if(_old != ""){
                this.formAdd.clocate = ""
                this.getCLocateList();
            }else{
                this.getCLocateList();
            }

      }
  },
  created(){
      this.getSelect();
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-top: 20px;
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
/*对话框*/
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



