<template>
  <div id="header">
      <div id="hd-lf">
          <h2 @click="TOINDEX">细菌耐药性信息管理系统</h2>
      </div>
      <div id="hd-rt">
          <ul v-if="log">
              <li>{{name}}</li>
              <li id="log-out" @click="LogOutFunc">退出</li>
          </ul>
      </div>
  </div>
</template>

<script>
// import Vue from 'vue'
// import VueRouter from 'vue-router'
// Vue.use(VueRouter)
import router from './../publicAPI/routes'
export default {
  components: {

  },
  props:[ 'log' ],
  data(){
      return {
          name: localStorage.getItem('account'),
      }
  },
  methods:{
      LogOutFunc(){
          localStorage.removeItem('account');
          this.$router.push({ path: '/login' });
      },
      TOINDEX(){
        //   alert(1);
          this.$router.push({ path:'/' })
      }
  }
}
</script>

<style scoped>
#header{
    width: 1340px;
    height: 54px;
    line-height: 54px;
    padding: 0 20px;
    color: #fff;
    margin: 0 auto;
    background: #1D8CE0;
    border-radius: 4px;
    margin-bottom: 20px;
}
#hd-lf{
    float: left;
}
#hd-lf h2{
    font-weight: normal;
    cursor: pointer;
}
#hd-rt{
    float: right;
}
#hd-rt ul{

}
#hd-rt ul li{
    float: left;
    cursor: pointer;
    margin: 0 10px;
}
#log-out{
    color: #E5E9F2;
}
</style>



