<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'

export default {
  // name: 'login',
  components: {
      myHeader,
      myLocation,
      myNavigat
  },
  data(){
      return {
          tableData: [{},{},{}],
          inputShow: false,
          multipleSelection: [],
          dialogFormVisible: false,
          form: {
              name: '',
              pass: '',
              role:[]
          },
          formLabelWidth: '120px'
      }
  },
  methods:{
      onAdd(){
          this.dialogFormVisible = true;
      },
      onEdit(){
          this.dialogFormVisible = true;
      },
      onDelete(){
         this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
           confirmButtonText: '确定',
           cancelButtonText: '取消',
           type: 'warning'
         }).then(() => {
           this.$message({
             type: 'success',
             message: '删除成功!'
           });
         }).catch(() => {
           this.$message({
             type: 'info',
             message: '已取消删除'
           });
         });
      },
      onConfirm(){
        this.inputShow = false;
        this.formSamInfo.LINKNAME = "";
        this.formSamInfo.LINKURL = "";
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
        //   console.log(this.multipleSelection);
      },
  },
  created(){
  }
}
</script>

