<!-- 采样地信息 | created by JanvierCheng -->
<template>
  <div id="sampleSiteInfoDiv">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="采样地信息"></my-location>
              <my-navigat activ="1-1"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="70px" :inline="true" class="demo-form-inline">
                      <el-form-item label="采样地" prop="CLOCATE">
                          <el-input v-model="formSamInfo.CLOCATE" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="详细地址" prop="ADDRESE">
                          <el-input v-model="formSamInfo.ADDRESE" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="联系电话" prop="TEL">
                          <el-input v-model="formSamInfo.TEL" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="联系人" prop="MEMENBER">
                          <el-input v-model="formSamInfo.MEMENBER" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="养殖模式" prop="YMODEL">
                          <el-input v-model="formSamInfo.YMODEL" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="所在省份" prop="PROVINCE">
                          <el-input v-model="formSamInfo.PROVINCE" size="small"></el-input>
                      </el-form-item>
                  </el-form>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                    <el-button-group>
                      <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                      <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                      <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                    </el-button-group>
                    <el-button-group>
                      <router-link to="/sampleSiteInfo/samSiteImpot"><el-button type="primary" icon="upload" style="border-radius: 5px 0 0 5px !important;">导入Excel</el-button></router-link>
                      <el-button type="primary" icon="d-caret" @click="onExport">导出Excel</el-button>
                    </el-button-group>
                    <el-button-group>
                      <el-button icon="search" @click="getList">搜索</el-button>
                      <el-button icon="close" @click="resetForm('formSamInfo')">重置</el-button>
                    </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height = "600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_CaiYang"
                      label="采样地名称"
                      width="200"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Province"
                      label="省份"
                      width="65">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Addres"
                      label="详细地址"
                      width="230"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Man"
                      label="联系人"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Tel"
                      label="联系电话"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_SiYang"
                      label="饲养方式"
                      width="82">
                    </el-table-column>
                    <el-table-column
                      prop="TB_GuiMo"
                      label="养殖规模"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_YModel"
                      label="养殖模式"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="BuildDate"
                      label="建厂时间"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Remark"
                      label="备注"
                      width="400">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="采样地信息" v-model="dialogFormVisible">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="采样地名称" :label-width="formLabelWidth" prop="caiY">
                      <el-input v-model="formAdd.caiY" auto-complete="off" :disabled="!currentOper"></el-input>
                    </el-form-item>
                    <el-form-item label="省份" :label-width="formLabelWidth" prop="provc">
                      <el-select  v-model="formAdd.provc" placeholder="选择省份">
                          <el-option :value="prvnc.name" v-for="prvnc in provc"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="详细地址" :label-width="formLabelWidth" prop="addres">
                      <el-input v-model="formAdd.addres" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="联系人" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="联系电话" :label-width="formLabelWidth" prop="tel">
                      <el-input v-model="formAdd.tel" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="饲养方式" :label-width="formLabelWidth" prop="siya">
                      <el-input v-model="formAdd.siya" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="养殖规模" :label-width="formLabelWidth" prop="guimo">
                      <el-input v-model="formAdd.guimo" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="养殖模式" :label-width="formLabelWidth" prop="moshi">
                      <el-select  v-model="formAdd.moshi" placeholder="选择养殖模式">
                          <el-option :value="moshi" v-for="moshi in yangModel"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="建厂时间" :label-width="formLabelWidth" prop="date1">
                        <el-date-picker type="date" placeholder="选择日期" v-model="formAdd.date1" :editable="false"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import axios from 'axios'
import dataSet from './../publicAPI/dataSet'
import dataRules from './../publicAPI/dataRules'
import config from './../publicAPI/config'
// import myInput from './components/inputGroup'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
    //   myInput
  },
  data(){
      return{
          rootURL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          formSamInfo: {
              CLOCATE: '',
              ADDRESE: '',
              TEL: '',
              MEMENBER: '',
              YMODEL: '',
              PROVINCE: ''
          },
          formAdd:{
              caiY: '',
              provc: '',
              man: '',
              tel: '',
              siya: '',
              guimo: '',
              moshi: '',
              date1: '',
              remark: '',
          },
          formAddRules:{
              caiY: [
                   { required: true, message: '请输入活动名称', trigger: 'blur' },
                   { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
              ],
              provc: [
                   { required: true, message: '请选择省份', trigger: 'change' },
              ],
              tel:[
                   { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
              ]
          },
          totalPage: 0, //总页数
          pageSize: 100, //每页记录数
          provc: dataSet.province,
          yangModel: dataSet.yangModel,
          switchSH: true,   //查询
          switchNV: true,   //导航
          loading2: true,    //表格加载中
          dialogFormVisible: false,
          formLabelWidth: '100px',
          currentPage: 1,   //当前页码
          currentOper: true,  //添加是true,修改是false
          cotDivWidth:{
              width: '1150px',
          }
      }
  },
  methods:{
        onConfirm(){//确定添加或修改
            let that = this
            this.$refs['formAdd'].validate((valid) => {
                  if (valid) {
                      var param = {
                          farmname: that.formAdd.caiY ,
                          farmprovince: that.formAdd.provc ,
                          farmlinkman: that.formAdd.man ,
                          farmphone: that.formAdd.tel ,
                          farmraiseway: that.formAdd.siya ,
                          farmraisescope: that.formAdd.guimo ,
                          farmraisetype: that.formAdd.moshi ,
                          farmbuilddate: dataRules.formatDate(that.formAdd.date1) ,
                          farmremarks: that.formAdd.remark ,
                      }
                      var str = JSON.stringify(param)
                      if(this.currentOper){ //添加
                          axios.post(that.rootURL+'/farm/addition', param)
                          .then(function (res) {
                              if(res.data.state){
                                  that.$message({
                                    type: 'success',
                                    message: res.data.value
                                  });
                                  that.dialogFormVisible = false
                                  that.getList()
                              }else{
                                  that.$message({
                                    type: 'error',
                                    message: res.data.value
                                  });
                              }
                          })
                          .catch(function (error) {
                          });
                      }else{ //修改
                          axios.post(that.rootURL+'/farm/update', param)
                          .then(function (res) {
                              if(res.data.state){
                                  that.$message({
                                    type: 'success',
                                    message: res.data.value
                                  });
                                  that.formAdd = []
                                  that.dialogFormVisible = false
                                  that.getList()
                              }else{
                                  that.$message({
                                    type: 'error',
                                    message: res.data.value
                                  });
                              }
                          })
                          .catch(function (error) {
                          });
                      }
                  } else {
                    console.log('error submit!!');
                    return false;
                  }
            });

        },
        onAdd(){//添加
            this.currentOper = true
            this.dialogFormVisible = true
        },
        onEdit(){//修改
            if(this.multipleSelection.length==1){
                this.currentOper = false
                this.dialogFormVisible = true
                this.formAdd = {
                    caiY: this.multipleSelection[0].TB_CaiYang,
                    provc: this.multipleSelection[0].TB_Province,
                    man: this.multipleSelection[0].TB_Man,
                    tel: this.multipleSelection[0].TB_Tel,
                    siya: this.multipleSelection[0].TB_SiYang,
                    guimo: this.multipleSelection[0].TB_GuiMo,
                    moshi: this.multipleSelection[0].TB_YModel,
                    date1: this.multipleSelection[0].BuildDate,
                    remark: this.multipleSelection[0].TB_Remark,
                }
            }
        },
        onDelete(){//删除
            if(this.multipleSelection.length>0){
                let that = this;
                this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then(() => {
                    let str = "";
                    for(var item of this.multipleSelection){
                        str = str + item.TB_CaiYang + ','
                    }
                    axios.post(that.rootURL+'/farm/deletion', {
                       farmname: str
                    })
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.getList();
                            // that.splice(
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                    });

                }).catch((error) => {
                    // this.$message.error(error);
                });
            }
        },
        onExport(){
            let that = this;
            if((this.multipleSelection).length > 0){
                var caiYStr = '';
                for ( var item of this.multipleSelection){
                    caiYStr = caiYStr + item.TB_CaiYang + ','
                }
                console.log(caiYStr)
                axios.get(that.rootURL + '/farm/download?farmname=' + caiYStr)
                    .then((res) => {
                        window.open(that.rootURL + '/farm/download?farmname=' + caiYStr)
                    })
                    .catch((error) =>{
                        console.error(error);
                    })
            }else{
                this.$message.error('请选择要导出的行！');
            }

        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        // onSearch(){
        //
        // },
        getList(){
            let that = this;
            var param = {
                farmaddress: that.formSamInfo.ADDRESE,
                farmlinkman: that.formSamInfo.MEMENBER,
                farmname: that.formSamInfo.CLOCATE,
                farmphone: that.formSamInfo.TEL,
                farmprovince: that.formSamInfo.PROVINCE,
                farmraisetype: that.formSamInfo.YMODEL,
                order: '',
                page: that.currentPage,
                rows: that.pageSize,
                sort: 'desc'
            }
            axios.post(that.rootURL+'/farm/list', param)
            .then(function (res) {
                that.tableData = [];
                that.totalPage = res.data.value.pages
                for( var item of res.data.value.data){
                    that.tableData.push({
                        TB_CaiYang: item.farmname,
                        TB_Province: item.farmprovince,
                        TB_Addres: item.farmaddress,
                        TB_Man: item.farmlinkman,
                        TB_Tel: item.farmphone,
                        TB_SiYang: item.farmraiseway,
                        TB_GuiMo: item.farmraisescope,
                        TB_YModel: item.farmraisetype,
                        BuildDate: item.farmbuilddate,
                        TB_Remark: item.farmremarks
                    })
                }
                that.loading2 = false;
            })
            .catch(function (error) {
              console.error(error);
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            if(formName == 'formSamInfo'){
                this.formSamInfo.date1 = "";
                this.formSamInfo.date2 = "";
                this.getList();
            }else if(formName == 'formAdd'){
                this.formAdd =  {
                    caiY: '',
                    provc: '',
                    man: '',
                    tel: '',
                    siya: '',
                    guimo: '',
                    moshi: '',
                    date1: '',
                    remark: '',
                }
                this.dialogFormVisible = false
            }
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
  },
  created(){
      this.getList();
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
     },
    //  dialogFormVisible(val){
    //      if(!val){
    //          this.$refs['formAdd'].resetFields();
    //      }
    //  }
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
/*表单输入组*/
.inputGp{
    margin-bottom: 20px;
}
.inputGp .el-form-item{
    margin-bottom: 2px;
}
.inputGp .el-input{
    width: 200px;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
/*对话框*/
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



