<!-- 耐药性基因 | created by JanvierCheng -->
<template>
  <div id="sampleSiteInfoDiv">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="耐药性基因信息"></my-location>
              <my-navigat activ="1-6"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="70px" :inline="true" class="demo-form-inline">
                      <el-form-item label="菌株编号" prop="ID">
                          <el-input v-model="formSamInfo.ID" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="内部编号" prop="INNO">
                          <el-input v-model="formSamInfo.INNO" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="耐药基因" prop="GENE">
                          <el-input v-model="formSamInfo.GENE" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="插入元件" prop="CHARU">
                          <el-input v-model="formSamInfo.CHARU" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="复制子" prop="COPY">
                          <el-input v-model="formSamInfo.COPY" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="操作人" prop="MAN">
                          <el-input v-model="formSamInfo.MAN" size="small"></el-input>
                      </el-form-item>
                  </el-form>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                    <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                    <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                  </el-button-group>
                  <el-button-group>
                    <router-link to="/geneBasicInfo/geneImpot"><el-button type="primary" icon="upload" style="border-radius: 5px 0 0 5px !important;">导入Excel</el-button></router-link>
                    <el-button type="primary" icon="d-caret">导出Excel</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button icon="search" @click="getList">搜索</el-button>
                    <el-button icon="close" @click="resetForm('formSamInfo')">重置</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height = "600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35" >
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      label="菌株编号"
                      width="200">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      label="内部编号"
                      width="140">
                    </el-table-column>
                    <el-table-column
                      prop="TB_gene"
                      label="耐药基因"
                      width="198">
                    </el-table-column>
                    <el-table-column
                      prop="TB_charu"
                      label="插入元件"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_copy"
                      label="复制子"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_jiehe"
                      label="接合子"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_man"
                      label="操作人"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="备注">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="采样地信息" v-model="dialogFormVisible">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="菌株编号" :label-width="formLabelWidth" prop="id">
                      <el-input v-model="formAdd.id" auto-complete="off" :disabled="!currentOper"></el-input>
                    </el-form-item>
                    <el-form-item label="内部编号" :label-width="formLabelWidth" prop="inno">
                      <el-input v-model="formAdd.inno" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="耐药基因" :label-width="formLabelWidth" prop="gene">
                      <el-input v-model="formAdd.gene" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="插入元件" :label-width="formLabelWidth" prop="charu">
                      <el-input v-model="formAdd.charu" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="复制子" :label-width="formLabelWidth" prop="copy">
                      <el-input v-model="formAdd.copy" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="结合子" :label-width="formLabelWidth" prop="jiehe">
                      <el-input v-model="formAdd.jiehe" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="操作人" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import axios from 'axios'
import config from './../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
    //   myInput
  },
  data(){
      return{
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          formSamInfo: {
              ID: '',
              INNO: '',
              GENE: '',
              CHARU: '',
              COPY: '',
              MAN: ''
          },
          formAdd:{
              id: '',
              inno: '',
              gene: '',
              charu: '',
              copy: '',
              jiehe: '',
              man: '',
              remark: '',
          },
          formAddRules:{
              id: [
                   { required: true, message: '请输入菌株编号', trigger: 'blur' },
                   { min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }
              ],
          },
          switchSH: true,   //查询
          switchNV: true,   //导航
          cotDivWidth:{
              width: '1150px',
          },
          totalPage: 0, //总页数
          pageSize: 100, //每页记录数
          loading2: true,    //表格加载中
          dialogFormVisible: false,
          formLabelWidth: '85px',
          currentPage: 1,   //当前页码
          currentOper: true,  //添加是true,修改是false
      }
  },
  methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        onAdd(){//添加
            this.currentOper = true
            this.dialogFormVisible = true
        },
        onEdit(){//修改
            if(this.multipleSelection.length==1){
                this.currentOper = false
                this.dialogFormVisible = true
                this.formAdd = {
                    id: this.multipleSelection[0].TB_id,
                    inno: this.multipleSelection[0].TB_inno,
                    gene: this.multipleSelection[0].TB_gene,
                    charu: this.multipleSelection[0].TB_charu,
                    copy: this.multipleSelection[0].TB_copy,
                    jiehe: this.multipleSelection[0].TB_jiehe,
                    man: this.multipleSelection[0].TB_man,
                    remark: this.multipleSelection[0].TB_remark,
                    dataID: this.multipleSelection[0].TB_dataID,
                }
            }
        },
        onDelete(){//删除
            if(this.multipleSelection.length>0){
                let that = this;
                this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then(() => {
                    let str = "";
                    for(var item of this.multipleSelection){
                        str = str + item.TB_dataID + ','
                    }
                    axios.post(that.URL+'/character/deletion', {
                       otherMsg: str
                    })
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.getList();
                            // that.splice(
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                    });

                }).catch((error) => {
                    // this.$message.error(error);
                });
            }
        },
        getList(){
            let that = this
            var param = {
                strainno: that.formSamInfo.ID,
                genalias: that.formSamInfo.INNO,
                genname: that.formSamInfo.GENE,
                iseq: that.formSamInfo.CHARU,
                replicon: that.formSamInfo.COPY,
                operator: that.formSamInfo.MAN,
                page: that.currentPage,
                rows: that.pageSize,
            }
            axios.post( that.URL + '/character/list', param )
                .then((res) => {
                    that.tableData = [];
                    that.totalPage = res.data.value.pages
                    for( var item of res.data.value.data){
                        that.tableData.push({
                            TB_id: item.strainno,
                            TB_inno: item.genalias,
                            TB_gene: item.genname,
                            TB_charu: item.iseq,
                            TB_copy: item.replicon,
                            TB_jiehe: item.gentc,
                            TB_man: item.operator,
                            TB_remark: item.genremarks,
                            TB_dataID: item.id
                        })
                    }
                    that.loading2 = false;
                })
                .catch((error) => {
                    console.error(error);
                })
        },
        onConfirm(){
            let that = this
            this.$refs['formAdd'].validate((valid) => {
                  if (valid) {
                      var param = {
                          strainno: that.formAdd.id ,
                          genname: that.formAdd.gene ,
                          iseq: that.formAdd.charu ,
                          replicon: that.formAdd.copy ,
                          gentc: that.formAdd.jiehe ,
                          operator: that.formAdd.man ,
                          genremarks: that.formAdd.remark ,
                      }
                      if(this.currentOper){ //添加
                          axios.post(that.URL+'/character/addition', param)
                          .then(function (res) {
                              if(res.data.state){
                                  that.$message({
                                    type: 'success',
                                    message: res.data.value
                                  });
                                  that.dialogFormVisible = false
                                  that.getList()
                              }else{
                                  that.$message({
                                    type: 'error',
                                    message: res.data.value
                                  });
                              }
                          })
                          .catch(function (error) {
                          });
                      }else{ //修改
                          param.id = that.formAdd.dataID;
                          axios.post(that.URL+'/character/update', param)
                          .then(function (res) {
                              if(res.data.state){
                                  that.$message({
                                    type: 'success',
                                    message: res.data.value
                                  });
                                  that.formAdd = []
                                  that.dialogFormVisible = false
                                  that.getList()
                              }else{
                                  that.$message({
                                    type: 'error',
                                    message: res.data.value
                                  });
                              }
                          })
                          .catch(function (error) {
                          });
                      }
                  } else {
                    console.log('error submit!!');
                    return false;
                  }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            if(formName == 'formSamInfo'){
                this.getList();
            }else if(formName == 'formAdd'){
                this.formAdd =  {
                    id: '',
                    inno: '',
                    gene: '',
                    charu: '',
                    copy: '',
                    man: '',
                    remark: '',
                }
                this.dialogFormVisible = false
            }
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
      }
  },
  created(){
      this.getList();
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
/*表单输入组*/
.inputGp{
    margin-bottom: 20px;
}
.el-form-item{
    margin-bottom: 2px;
}
.el-input{
    width: 200px;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



