<template>
  <div id="location">
      <div class="locat-lf">
          <i class="el-icon-d-arrow-right"></i>
      </div>
      <div class="locat-rt">
          <h3>{{ title }}</h3>
      </div>
  </div>
</template>

<script>

export default {
    props:[ 'title' ],
    components: {

    }
}
</script>

<style scoped>
#location{
    background: #1D8CE0;
    width: 180px;
    height: 45px;
    line-height: 45px;
    color: #fff;
    padding: 0 20px;
    border-radius: 3px;
    margin-bottom: 10px;
}
#location .locat-lf{
    float: left;
    font-size: 8px;
}
#location .locat-rt{
    margin-left: 10px;
    float: left;
}
#location .locat-rt h3{
    font-weight: normal;
    font-size: 16px !important;
}
</style>



