<!-- 来源编号 | created by JanvierCheng -->
<template>
  <div id="sourceNumberDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="来源编号"></my-location>
              <my-navigat activ="3-2"></my-navigat>
          </div>
          <div class="ho-cotet">
              <div>
                  <el-button-group>
                    <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                    <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height="600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_sourceNo"
                      label="来源号"
                      width="240"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column
                      prop="TB_sourceName"
                      label="名称"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[10,20,30,40]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="来源编号信息" v-model="dialogFormVisible" >
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd">
                    <el-form-item label="来源号" :label-width="formLabelWidth" prop="sourceno">
                      <el-input v-model="formAdd.sourceno" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="名称" :label-width="formLabelWidth" prop="sourcename">
                      <el-input v-model="formAdd.sourcename" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>

  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import config from './../publicAPI/config'
import axios from 'axios'

export default {
  // name: 'login',
  components: {
      myHeader,
      myLocation,
      myNavigat
  },
  data(){
      return {
          URL: config.rootURL,
          formSamInfo: {
              SNAME: '',
              DNAME: '',
              STRAIN: ''
          },
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          formAdd: {
              sourceno: '',
              sourcename: '',
          },
          formAddRules:{
              sourceno: [
                  { required: true, message: '请输入来源号', trigger: 'blur' },
                  { min: 1, max: 11, message: '长度在 1 到 11 个字符', trigger: 'blur' }
              ],
              sourcename: [
                  { required: true, message: '请输入名称', trigger: 'blur' },
              ]
          },
          loading2: true,
          currentPage: 1,
          pageSize: 10,
          totalPage: 0,
          formLabelWidth: '80px'
      }
  },
  methods:{
      onAdd(){
          this.dialogFormVisible = true
      },
      onDelete(){
          if(this.multipleSelection.length>0){
              let that = this;
              this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                  let str = "";
                  for(var item of this.multipleSelection){
                      str = str + item.TB_sourceNo + ','
                  }
                  axios.post(that.URL+'/source/deletion', {
                     ids: str
                  })
                  .then(function (res) {
                      if(res.data.state){
                          that.$message({
                            type: 'success',
                            message: res.data.value
                          });
                          that.getList();
                      }else{
                          that.$message({
                            type: 'error',
                            message: res.data.value
                          });
                      }
                  })
                  .catch(function (error) {
                  });

              }).catch((error) => {
                  // this.$message.error(error);
              });
          }
      },
      onConfirm(){
          let that = this
          this.$refs['formAdd'].validate((valid) => {
              if (valid) {
                  var param = {
                      sourcename: that.formAdd.sourcename,
                      sourceno: that.formAdd.sourceno,
                  }
                  axios.post(that.URL+'/source/addition', param)
                  .then(function (res) {
                      if(res.data.state){
                          that.$message({
                            type: 'success',
                            message: res.data.value
                          });
                          that.dialogFormVisible = false
                          that.getList()
                      }else{
                          that.$message({
                            type: 'error',
                            message: res.data.value
                          });
                      }
                  })
                  .catch(function (error) {
                    console.error(error);
                  });
                  this.dialogFormVisible = false
              } else {
                console.log('error submit!!');
                return false;
              }
          });

      },
      getList(){
          let that = this;
          var param = {
              page: that.currentPage,
              rows: that.pageSize,
          }
          axios.post(that.URL+'/source/list', param)
          .then(function (res) {
              that.tableData = [];
              that.totalPage = res.data.value.pages
              for( var item of res.data.value.data){
                  that.tableData.push({
                      TB_sourceNo: item.sourceno,
                      TB_sourceName: item.sourcename
                  })
              }
              that.loading2 = false;
          })
          .catch(function (error) {
            console.error(error);
          });
      },
      resetForm(formName){
          this.$refs[formName].resetFields();
          if(formName == 'formSamInfo'){
              this.getList();
          }else if(formName == 'formAdd'){
              this.formAdd =  {
                  sourceno: '',
                  sourcename: '',
              }
              this.dialogFormVisible = false
          }
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
        //   console.log(this.multipleSelection);
      },
      handleSizeChange(val) {
          this.pageSize = val;
          this.getList();
          console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
          this.currentPage = val;
          this.getList();
          console.log(`当前页: ${val}`);
      }
  },
  created(){
      this.getList();
  },
  watch:{
    //   'dialogFormVisible':function(){
    //       if(!this.dialogFormVisible){
    //           this.$refs['formAdd'].resetFields();
    //       }
    //   }
  }

}
</script>

<style scoped>
.el-button-group{
    margin-bottom: 30px;
}
.inputGp{
    margin-bottom: 20px;
}
.inputGp .el-input{
    width: 240px;
}
.el-form-item{
    margin-bottom: 0 !important;
}
.block{
    margin-top: 30px;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
</style>



