<!-- 用户列表 | created by JanvierCheng -->
<template>
  <div id="userListDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="用户列表"></my-location>
              <my-navigat activ="4-1"></my-navigat>
          </div>
          <div class="ho-cotet">
              <div>
                  <el-button-group>
                    <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                    <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                    <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="50" >
                    </el-table-column>
                    <el-table-column
                      prop="TB_linkname"
                      label="用户名"
                      width="250"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column
                      prop="TB_linkurl"
                      label="密码"
                      width="250"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column
                      prop="TB_linkurl"
                      label="角色"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                  </el-table>
              </div>
              <el-dialog title="用户信息" v-model="dialogFormVisible">
                  <el-form :model="form">
                    <el-form-item label="用户名" :label-width="formLabelWidth">
                      <el-input v-model="form.name" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" :label-width="formLabelWidth">
                      <el-input v-model="form.pass" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="用户角色" :label-width="formLabelWidth">
                      <el-select v-model="form.role" multiple placeholder="请选择活动区域">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                      </el-select>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>

  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'

export default {
  // name: 'login',
  components: {
      myHeader,
      myLocation,
      myNavigat
  },
  data(){
      return {
          tableData: [{},{},{}],
          inputShow: false,
          multipleSelection: [],
          dialogFormVisible: false,
          form: {
              name: '',
              pass: '',
              role:[]
          },
          formLabelWidth: '120px'
      }
  },
  methods:{
      onAdd(){
          this.dialogFormVisible = true;
      },
      onEdit(){
          this.dialogFormVisible = true;
      },
      onDelete(){
         this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
           confirmButtonText: '确定',
           cancelButtonText: '取消',
           type: 'warning'
         }).then(() => {
           this.$message({
             type: 'success',
             message: '删除成功!'
           });
         }).catch(() => {
           this.$message({
             type: 'info',
             message: '已取消删除'
           });
         });
      },
      onConfirm(){
        this.inputShow = false;
        this.formSamInfo.LINKNAME = "";
        this.formSamInfo.LINKURL = "";
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
        //   console.log(this.multipleSelection);
      },
  },

}
</script>

<style scoped>
.el-button-group{
    margin-top: 5px;
    margin-bottom: 20px;
}
.el-input,.el-select{
    width: 500px;
}
</style>



