<!-- MIC信息（阳性） | created by JanvierCheng -->
<template>
  <div id="micInfoYangDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="MIC信息-阳性"></my-location>
              <my-navigat activ="1-4"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="68px" :inline="true" class="demo-form-inline">
                      <el-form-item label="菌株编号" prop="ID">
                          <el-input v-model="formSamInfo.ID"></el-input>
                      </el-form-item>
                      <el-form-item label="内部编号" prop="INNO">
                          <el-input v-model="formSamInfo.INNO"></el-input>
                      </el-form-item>
                      <!-- <el-form-item label="列显示" prop="HIDECOL" >
                          <el-select  v-model="formSamInfo.HIDECOL" multiple placeholder="选择隐藏列" style="width:320px !important;">
                              <el-option :value="ydrug" v-for="ydrug in YangShow"></el-option>
                          </el-select>
                      </el-form-item> -->
                      <el-form-item label="药品查找" prop="DRUGRANGE">
                          <el-select  v-model="formSamInfo.DRUGRANGE" placeholder="选药品" style="width:120px !important;">
                              <el-option :value="ydrug" v-for="ydrug in YangDrugSet"></el-option>
                          </el-select>
                          &nbsp;从&nbsp;
                          <el-input v-model="formSamInfo.DRUGRANGE_S" style="width:56px !important;"></el-input>
                          &nbsp;到&nbsp;
                          <el-input v-model="formSamInfo.DRUGRANGE_E" style="width:56px !important;"></el-input>

                      </el-form-item>
                      <el-button icon="plus" @click="addRange">添加范围</el-button>
                  </el-form>
                  <myRange :items="add_range"></myRange>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                    <el-button-group>
                      <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                      <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                      <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                    </el-button-group>
                    <el-button-group>
                      <router-link to="/micInfoYang/micYangImpot"><el-button type="primary" icon="upload" style="border-radius: 5px 0 0 5px !important;">导入Excel</el-button></router-link>
                      <el-button type="primary" icon="d-caret">导出Excel</el-button>
                    </el-button-group>
                    <el-button-group>
                      <el-button icon="search" @click="getList">搜索</el-button>
                      <el-button icon="close" @click="resetForm('formSamInfo')">重置</el-button>
                    </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%;"
                    max-height="600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      fixed
                      label="菌株编号"
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      fixed
                      label="内部编号"
                      width="100">
                    </el-table-column>
                    <!-- <el-table-column
                      prop="TB_AMP"
                      label="AMP"
                      width="52"
                      v-if="showCOL.AMP">
                    </el-table-column> -->
                    <el-table-column
                      :prop="item"
                      :label="item"
                      width="52"
                      v-for="item in YangSeq">
                    </el-table-column>
                    <!-- <el-table-column
                      prop="TB_CIP"
                      label="CIP"
                      width="52"
                      v-if="showCOL.CIP">
                    </el-table-column>
                    <el-table-column
                      prop="TB_CLI"
                      label="CLI"
                      width="52"
                      v-if="showCOL.CLI">
                    </el-table-column>
                    <el-table-column
                      prop="TB_CQM"
                      label="CQM"
                      width="52"
                      v-if="showCOL.CQM">
                    </el-table-column>
                    <el-table-column
                      prop="TB_ERY"
                      label="ERY"
                      width="52"
                      v-if="showCOL.ERY">
                    </el-table-column>
                    <el-table-column
                      prop="TB_FFC"
                      label="FFC"
                      width="52"
                      v-if="showCOL.FFC">
                    </el-table-column>
                    <el-table-column
                      prop="TB_FOX"
                      label="FOX"
                      width="52"
                      v-if="showCOL.FOX">
                    </el-table-column>
                    <el-table-column
                      prop="TB_GEN"
                      label="GEN"
                      width="52"
                      v-if="showCOL.GEN">
                    </el-table-column>
                    <el-table-column
                      prop="TB_LZD"
                      label="LZD"
                      width="52"
                      v-if="showCOL.LZD">
                    </el-table-column>
                    <el-table-column
                      prop="TB_OXA"
                      label="OXA"
                      width="52"
                      v-if="showCOL.OXA">
                    </el-table-column>
                    <el-table-column
                      prop="TB_PEN"
                      label="PEN"
                      width="52"
                      v-if="showCOL.PEN">
                    </el-table-column>
                    <el-table-column
                      prop="TB_RIF"
                      label="RIF"
                      width="52"
                      v-if="showCOL.RIF" >
                    </el-table-column>
                    <el-table-column
                      prop="TB_TET"
                      label="TET"
                      width="52"
                      v-if="showCOL.TET">
                    </el-table-column>
                    <el-table-column
                      prop="TB_TIA"
                      label="TIA"
                      width="52"
                      v-if="showCOL.TIA">
                    </el-table-column>
                    <el-table-column
                      prop="TB_VAL"
                      label="VAL"
                      width="52"
                      v-if="showCOL.VAL">
                    </el-table-column>
                    <el-table-column
                      prop="TB_VAN"
                      label="VAN"
                      width="52"
                      v-if="showCOL.VAN">
                    </el-table-column> -->
                    <el-table-column
                      prop="TB_man"
                      label="实验人员"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_style"
                      label="实验方式">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="备注">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="MIC耐药性信息-阳性" v-model="dialogFormVisible" size="large">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="菌株编号" :label-width="formLabelWidth" prop="id">
                      <el-input v-model="formAdd.id" auto-complete="off"  :disabled="!currentOpert"></el-input>
                    </el-form-item>
                    <el-form-item label="实验人员" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="AMP" :label-width="formLabelWidth" prop="AMP">
                        <el-input v-model="formAdd.AMP" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CIP" :label-width="formLabelWidth" prop="CIP">
                        <el-input v-model="formAdd.CIP" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CLI" :label-width="formLabelWidth" prop="CLI">
                        <el-input v-model="formAdd.CLI" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CQM" :label-width="formLabelWidth" prop="CQM">
                        <el-input v-model="formAdd.CQM" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="ERY" :label-width="formLabelWidth" prop="ERY">
                        <el-input v-model="formAdd.ERY" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="FFC" :label-width="formLabelWidth" prop="FFC">
                        <el-input v-model="formAdd.FFC" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="FOX" :label-width="formLabelWidth" prop="FOX">
                        <el-input v-model="formAdd.FOX" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="GEN" :label-width="formLabelWidth" prop="GEN">
                        <el-input v-model="formAdd.GEN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="LZD" :label-width="formLabelWidth" prop="LZD">
                        <el-input v-model="formAdd.LZD" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="OXA" :label-width="formLabelWidth" prop="OXA">
                        <el-input v-model="formAdd.OXA" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="PEN" :label-width="formLabelWidth" prop="PEN">
                        <el-input v-model="formAdd.PEN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="RIF" :label-width="formLabelWidth" prop="RIF">
                        <el-input v-model="formAdd.RIF" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="TET" :label-width="formLabelWidth" prop="TET">
                        <el-input v-model="formAdd.TET" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="TIA" :label-width="formLabelWidth" prop="TIA">
                        <el-input v-model="formAdd.TIA" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="VAL" :label-width="formLabelWidth" prop="VAL">
                        <el-input v-model="formAdd.VAL" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="VAN" :label-width="formLabelWidth" prop="VAN">
                        <el-input v-model="formAdd.VAN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="实验方式" :label-width="formLabelWidth" prop="style">
                      <el-input v-model="formAdd.style" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import dataSet from './../publicAPI/dataSet'
import dataRules from './../publicAPI/dataRules'
import rootURL from './../publicAPI/config'
import myRange from './../components/addDrugRange'
import axios from 'axios';
import config from './../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
      myRange
  },
  data(){
      return{
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          currentOpert: true, //true是新增，false是修改
          formSamInfo: {
                ID: '',
                INNO: '',
                HIDECOL: [],
                DRUGRANGE: '',
                DRUGRANGE_S: '',
                DRUGRANGE_E: '',
          },
          add_range: [ //添加药物筛选范围
          ],
          formAdd: {
              id: '',
              inno: '',
              AMP: '',
              CIP: '',
              CLI: '',
              CQM: '',
              ERY: '',
              FFC: '',
              FOX: '',
              GEN: '',
              LZD: '',
              OXA: '',
              PEN: '',
              RIF: '',
              TET: '',
              TIA: '',
              VAL: '',
              VAN: '',
              man: '',
              style: '',
              remark: '',
          },
          formAddRules: {
              id: [
                  { required: true, message: '请输入菌株编号', trigger: 'blur' },
                  { min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }
              ]
          },
          switchSH: true,   //查询
          switchNV: true,   //导航
          loading2: false,
          currentPage: 1,
          pageSize: 100,
          totalPage: 0,
          formLabelWidth: '84px',
          cotDivWidth:{
              width: '1150px',
          },
          YangDrugSet: [],
          YangSeq: [],
      }
  },
  methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        onAdd(){
            this.dialogFormVisible = true
            this.currentOpert = true
        },
        onEdit(){
            if(this.multipleSelection.length==1){
                this.dialogFormVisible = true
                this.currentOpert = false
                this.formAdd = {
                    id: this.multipleSelection[0].TB_id,
                    AMP: this.multipleSelection[0].AMP,
                    CIP: this.multipleSelection[0].CIP,
                    CLI: this.multipleSelection[0].CLI,
                    CQM: this.multipleSelection[0].CQM,
                    ERY: this.multipleSelection[0].ERY,
                    FFC: this.multipleSelection[0].FFC,
                    FOX: this.multipleSelection[0].FOX,
                    GEN: this.multipleSelection[0].GEN,
                    LZD: this.multipleSelection[0].LZD,
                    OXA: this.multipleSelection[0].OXA,
                    PEN: this.multipleSelection[0].PEN,
                    RIF: this.multipleSelection[0].RIF,
                    TET: this.multipleSelection[0].TET,
                    TIA: this.multipleSelection[0].TIA,
                    VAL: this.multipleSelection[0].VAL,
                    VAN: this.multipleSelection[0].VAN,
                    man: this.multipleSelection[0].TB_man,
                    style: this.multipleSelection[0].TB_style,
                    remark: this.multipleSelection[0].TB_remark,
                }
            }
        },
        onDelete(){
            if(this.multipleSelection.length>0){
                let that = this;
                this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then(() => {
                    let str = "";
                    for(var item of this.multipleSelection){
                        str = str + item.TB_id + ','
                    }
                    axios.post(that.URL+'/mic/positive/deletion', {
                       strainno: str
                    })
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.getList();
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                    });

                }).catch((error) => {
                    // this.$message.error(error);
                });
            }
        },
        onConfirm(){
            let that = this
            var param = {
                strainno: this.formAdd.id,
                amp: parseFloat(this.formAdd.AMP),
                cip: parseFloat(this.formAdd.CIP),
                cli: parseFloat(this.formAdd.CLI),
                cqm: parseFloat(this.formAdd.CQM),
                ery: parseFloat(this.formAdd.ERY),
                ffc: parseFloat(this.formAdd.FFC),
                fox: parseFloat(this.formAdd.FOX),
                gen: parseFloat(this.formAdd.GEN),
                lzd: parseFloat(this.formAdd.LZD),
                oxa: parseFloat(this.formAdd.OXA),
                pen: parseFloat(this.formAdd.PEN),
                rif: parseFloat(this.formAdd.RIF),
                tet: parseFloat(this.formAdd.TET),
                tia: parseFloat(this.formAdd.TIA),
                val: parseFloat(this.formAdd.VAL),
                van: parseFloat(this.formAdd.VAN),
                operator: this.formAdd.man,
                micdetectiontype: this.formAdd.style,
                remark: this.formAdd.remark
            }
            this.$refs['formAdd'].validate((valid) => {
                if (valid) {
                    if( this.currentOpert){//新增
                        axios.post(that.URL+'/mic/positive/addition', param)
                        .then(function (res) {
                            if(res.data.state){
                                that.$message({
                                  type: 'success',
                                  message: res.data.value
                                });
                                that.dialogFormVisible = false
                                that.getList()
                            }else{
                                that.$message({
                                  type: 'error',
                                  message: res.data.value
                                });
                            }
                        })
                        .catch(function (error) {
                          console.error(error);
                        });
                        this.dialogFormVisible = false
                    }else{//修改
                        axios.post(that.URL+'/mic/positive/update', param)
                        .then(function (res) {
                            if(res.data.state){
                                that.$message({
                                  type: 'success',
                                  message: res.data.value
                                });
                                that.dialogFormVisible = false
                                that.getList()
                            }else{
                                that.$message({
                                  type: 'error',
                                  message: res.data.value
                                });
                            }
                        })
                        .catch(function (error) {
                          console.error(error);
                        });
                        this.dialogFormVisible = false
                    }
                } else {
                  console.log('error submit!!');
                  return false;
                }
            });
        },
        getList(){
            let that = this
            var param = {
                strainno : that.formSamInfo.ID,
                micalias : that.formSamInfo.INNO,
                page: that.currentPage,
                rows: that.pageSize,
            }
            for( var item of that.add_range){
                param[(item.R).toLowerCase()+'1'] = parseFloat(item.S)
                param[(item.R).toLowerCase()+'2'] = parseFloat(item.E)
            }
            // console.warn(param)
            that.loading2 = true

            //把药品查找范围的药品在table前面列显示
            if((that.add_range).length>=1){
                that.YangSeq = [];
                (that.add_range).forEach((range, i) => {
                    (that.YangSeq)[i] = range.R;
                });
                var tmp = [];
                for(var item of dataSet.yangDrug){
                    tmp.push(item);
                }
                (that.add_range).forEach((range, i) => {
                    tmp.forEach((val, j) => {
                        if(val == range.R){
                            tmp.splice(j,1)
                        }
                    })
                });
                tmp.forEach((val, j) => {
                    (that.YangSeq).push(val);
                });
            }else{
                that.YangSeq = dataSet.yangDrug
                // console.warn(dataSet.yangDrug)
            }
            // console.log(that.YangSeq)
            axios.post( that.URL + '/mic/positive/list', param)
                .then((res) => {
                    if(res.data.state){
                        that.tableData = []
                        that.totalPage = res.data.value.pages

                        for(var item of res.data.value.data){

                            that.tableData.push({
                                TB_id: item.strainno,
                                TB_inno: item.micalias,
                                AMP: item.amp,
                                CIP: item.cip,
                                CLI: item.cli,
                                CQM: item.cqm,
                                ERY: item.ery,
                                FFC: item.ffc,
                                FOX: item.fox,
                                GEN: item.gen,
                                LZD: item.lzd,
                                OXA: item.oxa,
                                PEN: item.pen,
                                RIF: item.rif,
                                TET: item.tet,
                                TIA: item.tia,
                                VAL: item.val,
                                VAN: item.van,
                                TB_man: item.operator,
                                TB_style: item.micdetectiontype,
                                TB_remark: item.remark,
                            })
                        }
                        that.loading2 = false
                    }
                })
                .catch((error) => {
                    console.error(error);
                })
        },
        addRange(){ //添加药品范围
            for(var item of this.add_range){
                if(this.formSamInfo.DRUGRANGE == item.R){
                    this.$message.error('该筛选已添加，无须重复添加！');
                    return false;
                }
            }
            if(dataRules.serDrugRule(this.formSamInfo.DRUGRANGE, this.formSamInfo.DRUGRANGE_S, this.formSamInfo.DRUGRANGE_E)){
                var that = this;
                this.add_range.push({
                    R:this.formSamInfo.DRUGRANGE,  S:this.formSamInfo.DRUGRANGE_S, E:this.formSamInfo.DRUGRANGE_E
                })
            }else{this.$message.error('输入不能为空，范围从小到大(0-2048)，且为数字！');}
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            if(formName == 'formSamInfo'){
                this.add_range = []
                this.formSamInfo.DRUGRANGE_S = "";
                this.formSamInfo.DRUGRANGE_E = "";
                // this.YangSeq = dataSet.yangDrug
                this.getList()
            }else if(formName == 'formAdd'){
                this.formAdd = {
                    id: '',
                    inno: '',
                    AMP: '',
                    CIP: '',
                    CLI: '',
                    CQM: '',
                    ERY: '',
                    FFC: '',
                    FOX: '',
                    GEN: '',
                    LZD: '',
                    OXA: '',
                    PEN: '',
                    RIF: '',
                    TET: '',
                    TIA: '',
                    VAL: '',
                    VAN: '',
                    man: '',
                    style: '',
                    remark: '',
                }
                this.dialogFormVisible = false
            }
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
     },
  },
  created(){
      for(var item of dataSet.yangDrug){
          (this.YangDrugSet).push(item);
          (this.YangSeq).push(item);
      }
      this.getList();
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
    /*overflow: hidden;*/
}
/*表单输入组*/
.inputGp{
    margin-bottom: 30px;
}
.el-form-item{
    margin-bottom: 6px;
}
.el-input, .el-select, .el-date-editor{
    width: 160px !important;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-bottom: 30px;
}
.tableBox{
