<!-- 主页设置 | created by JanvierCheng -->
<template>
  <div id="homeSettingDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="主页设置"></my-location>
              <my-navigat activ="4-3"></my-navigat>
          </div>
          <div class="ho-cotet">
              <div class="inputGp">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="55px" :inline="true" class="demo-form-inline">
                      <el-button-group>
                        <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                        <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                        <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                      </el-button-group>
                      <el-form-item label="链接名" v-if="inputShow">
                          <el-input v-model="formSamInfo.LINKNAME" style="width:240px !important;" :disabled="!currentOpert"></el-input>
                      </el-form-item>
                      <el-form-item label="地址" v-if="inputShow">
                          <el-input v-model="formSamInfo.LINKURL" style="width:240px !important;"></el-input>
                      </el-form-item>
                      <el-button @click="onConfirm" style="margin-left: 40px;" v-if="inputShow">确定</el-button>
                  </el-form>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35" >
                    </el-table-column>
                    <el-table-column
                      prop="TB_linkname"
                      label="链接名"
                      width="350"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column
                      prop="TB_linkurl"
                      label="链接地址"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="clearfix"></div>
          </div>
      </div>

  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import axios from 'axios';
import config from './../publicAPI/config'

export default {
  // name: 'login',
  components: {
      myHeader,
      myLocation,
      myNavigat
  },
  data(){
      return {
          URL: config.rootURL,
          currentOpert: true, //true是新增，false是修改
          formSamInfo: {
              LINKNAME: '',
              LINKURL: ''
          },
          tableData: [],
          inputShow: false,
          multipleSelection: []
      }
  },
  methods:{
      onAdd(){
          this.inputShow = true;
          this.currentOpert = true;
      },
      onEdit(){
          if(this.multipleSelection.length==1){
              this.inputShow = true;
              this.currentOpert = false;
              this.formSamInfo = {
                  LINKNAME: this.multipleSelection[0].TB_linkname,
                  LINKURL: this.multipleSelection[0].TB_linkurl,
              }
          }
      },
      onDelete(){
          if(this.multipleSelection.length>0){
              let that = this;
              this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                  let str = "";
                  for(var item of this.multipleSelection){
                      str = str + item.TB_linkname + ','
                  }
                  axios.post(that.URL+'/linkurl/deletion', {
                     name: str
                  })
                  .then(function (res) {
                      if(res.data.state){
                          that.$message({
                            type: 'success',
                            message: res.data.value
                          });
                          that.getList();
                      }else{
                          that.$message({
                            type: 'error',
                            message: res.data.value
                          });
                      }
                  })
                  .catch(function (error) {
                  });

              }).catch((error) => {
                  // this.$message.error(error);
              });
          }
      },
      getList(){
          let that = this
          axios.post(that.URL + '/linkurl/list')
            .then((res) => {
                that.tableData = []
                for(var item of res.data.value.data)
                that.tableData.push({
                    TB_linkname: item.name,
                    TB_linkurl: item.url
                })
            })
            .catch((error) => {
                console.error(error);
            })
      },
      onConfirm(){
        if(this.tableData.length<=9){
            let that = this
            var param = {
                name: this.formSamInfo.LINKNAME,
                url: this.formSamInfo.LINKURL,
            }
            if(that.currentOpert){ //添加
                axios.post(that.URL + '/linkurl/addition', param)
                    .then((res) => {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.getList()
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch((error) => {
                        console.error(error);
                    })
            }else{ //修改
                axios.post(that.URL + '/linkurl/update', param)
                    .then((res) => {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.getList()
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch((error) => {
                        console.error(error);
                    })
            }
            this.inputShow = false;
            this.formSamInfo.LINKNAME = "";
            this.formSamInfo.LINKURL = "";
        }else{
            this.$message.error('首页链接数暂时不能超过10个！');
        }
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
        //   console.log(this.multipleSelection);
      },
  },
  created(){
      this.getList()
  }
}
</script>

<style scoped>
.el-button-group{
    margin-right: 40px;
}
.inputGp{
    /*margin-top: 10px;*/
    margin-bottom: 20px;
}
.el-form-item{
    margin-bottom: 0 !important;
}
</style>



