<template>
  <div id="login">
       <my-header :log="false"></my-header>
       <div id="log-cont">
           <div style="float:left;width:760px;">
               <el-carousel indicator-position="outside" height="340px">
                <el-carousel-item>
                  <img src="./assets/img/lo4.jpg" height="340px" />
                </el-carousel-item>
                <el-carousel-item>
                  <img src="./assets/img/lo4.jpg" height="340px" />
                </el-carousel-item>
                <el-carousel-item>
                  <img src="./assets/img/lo4.jpg" height="340px" />
                </el-carousel-item>

              </el-carousel>
           </div>
           <div id="log-card">
               <h4>欢迎使用，请登陆！</h4>
               <el-form ref="form" :model="form" label-width="80px">
                       <el-input v-model="form.name" placeholder="用户名" size="large"></el-input>
                       <el-input v-model="form.pass" type="password" placeholder="密码" size="large"></el-input>
                   <el-button type="primary" @click="onSubmit" :loading="logined" style="font-family:'Microsoft Yahei';">登陆</el-button>
               </el-form>
           </div>
           <div class="clearfix"></div>
       </div>
       <my-footer></my-footer>
  </div>
</template>

<script>
import myHeader from './components/header'
import myFooter from './components/footer'
import config from './publicAPI/config'
import axios from 'axios'
export default {
  components: {
      myHeader,
      myFooter
  },
  data() {
      return {
          URL: config.rootURL,
          logined: false,
          form: {
              name: '',
              pass: '',
          }
      };
  },
  methods:{
      onSubmit(){
          let that = this;
          this.logined = true;
          var param = {
              username: this.form.name,
              password: this.form.pass,
          }
          axios.post(that.URL + '/user/login' , param)
            .then((res) => {
                if(res.data.state){
                    that.logined = false;
                    this.$message({
                      message: '登陆成功！',
                      type: 'success'
                    });
                    localStorage.setItem('account', this.form.name );
                    this.$router.push({ path: '/' });
                }else{
                    this.$message.error(res.data.value);
                }

            })
            .catch((error) => {
                console.error(error);
            })

      }
  }
}
</script>

<style scoped>
#login{
    width: 1340px;
    margin: 0 auto;
    border-radius: 4px;
}
#log-cont{
    background-color: #EFF2F7;
    padding: 60px;
}
#log-card{
    width: 360px;
    height: 340px;
    margin: 0 auto;
    float: right;
    text-align: center;
    /*border: 1px solid #99A9BF;*/
    border-radius: 4px;
    background: #fff;
    padding: 40px 34px 0;
}
#log-card h4{
    font-weight: normal;
    font-size: 18px;
    color: #656565;
    margin-bottom: 30px;
}
.el-input{
    /*font-family: 'Microsoft Yahei' !important;*/
    margin: 8px 0;
    font-size: 15px;
    height: 40px !important;
    color: #656565 !important;
}
.el-button{
    width: 292px;
    height: 44px;
    margin: 16px 0;
    font-size: 16px;
}
.el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
}

.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
}
</style>



