<template>
  <div id="samImpotDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="菌株基本信息"></my-location>
              <my-navigat activ="1-3"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-upload
                    :action="uploadURL"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :on-success="succeedUpload"
                    :on-error="failUpload"
                    :default-file-list="fileList">
                    <el-button size="small" type="primary">点击上传</el-button>
                    <div class="el-upload__tip" slot="tip">只能上传excel文件，excel的格式模板可点击下方按钮下载</div>
                  </el-upload>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button icon="d-arrow-left" @click="goBack">返回</el-button>
                    <el-button icon="caret-bottom" type="primary" @click="downLoadFile">下载导入模板</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button type="primary" icon="edit" @click="onEdit" style="border-radius:4px;">修改</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%;"
                    max-height="600"
                    @select="deleteRow"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      fixed
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      label="菌株编号"
                      width="124">
                    </el-table-column>
                    <el-table-column
                      prop="TB_xueqing"
                      label="血清型"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_mlst"
                      label="MLST分型"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_plg"
                      label="PLG分型"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="菌株备注"
                      width="260">
                    </el-table-column> -->
                    <el-table-column
                      prop="TB_failReason"
                      fixed="right"
                      label="失败原因"
                      width="400">
                    </el-table-column>
                  </el-table>
              </div>
              <el-dialog title="菌株基本信息" v-model="dialogFormVisible" >
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="菌株编号" :label-width="formLabelWidth" prop="id">
                      <el-input v-model="formAdd.id" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="血清型" :label-width="formLabelWidth" prop="xuqing">
                      <el-input v-model="formAdd.xuqing" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="MLST分型" :label-width="formLabelWidth" prop="mlst">
                      <el-input v-model="formAdd.mlst" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="PLG分型" :label-width="formLabelWidth" prop="plg">
                      <el-input v-model="formAdd.plg" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="菌株备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../../components/header'
import myLocation from './../../components/location'
import myNavigat from './../../components/navigat'
import axios from 'axios'
import dataSet from './../../publicAPI/dataSet'
import dataRules from './../../publicAPI/dataRules'
import config from './../../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data() {
      return {
          URL: config.rootURL,
          uploadURL: config.rootURL + '/strain/strain/upload',
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          formAdd: {
              id: "",
              xuqing: "",
              mlst: "",
              plg: "",
              remark: "",
          },
          formAddRules: {

          },
          switchSH: true,   //查询
          switchNV: true,   //导航
          loading2: false,
          formLabelWidth: '90px',
          cotDivWidth:{
              width: '1150px',
          },
          junshuSelect: [],
          junzhongSelect: [], //查询的---所有菌属
          junzhongspec: [], //新增先选菌属---部分菌属
          fileList: [
          ],
          selectRow: '',
      };
  },
  methods:{
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        deleteRow(selection, row){
            this.selectRow = row.TB_id
        },
        onEdit(){
            if(this.multipleSelection.length==1){
                this.dialogFormVisible = true
                this.formAdd = {
                    id: this.multipleSelection[0].TB_id,
                    xuqing: this.multipleSelection[0].TB_xueqing,
                    mlst: this.multipleSelection[0].TB_mlst,
                    plg: this.multipleSelection[0].TB_plg,
                    remark: this.multipleSelection[0].TB_remark,
                }
            }
        },
        onConfirm(){
            let that = this
            this.$refs['formAdd'].validate((valid) => {
                  if (valid) {
                      var param = {
                          strainno: that.formAdd.id,
                          serotype: this.formAdd.xuqing,
                          strainmlst: this.formAdd.mlst,
                          strainplg: this.formAdd.plg,
                          strainremarks: this.formAdd.remark,
                      }
                      var str = JSON.stringify(param)
                      axios.post(that.URL+'/strain/update', param)
                      .then(function (res) {
                          if(res.data.state){
                              that.$message({
                                type: 'success',
                                message: res.data.value
                              });
                              that.tableData.forEach((item, index) => {
                                //   console.log(item.TB_id+' --- '+that.selectRow)
                                  if(item.TB_id == that.selectRow){
                                      that.tableData.splice(index, 1);
                                  }
                              });
                          }else{
                              that.$message({
                                type: 'error',
                                message: res.data.value
                              });
                          }
                      })
                      .catch(function (error) {
                          console.error(error);
                      });
                      that.dialogFormVisible = false
                  } else {
                    console.log('error submit!!');
                    return false;
                  }
            });
        },
        resetForm(formName){
            this.$refs[formName].resetFields();
            this.formAdd = {
                id: "",
                xuqing: "",
                mlst: "",
                plg: "",
                remark: "",
            }
            this.dialogFormVisible = false
        },
        succeedUpload(response, file, fileList){
            let that = this;
            if(response.state){
                this.$message({
                  message: response.value,
                  duration: 5000,
                  type: 'success'
                });

            }else{
                if(typeof(response.value) != 'string'){
                    this.$message({
                      message: '成功导入【' + response.value.success + '】条，有【' + response.value.error + '】条导入失败！',
                      duration: 5000,
                      type: 'warning'
                    });
                    /*插入数据*/
                    that.tableData = [];
                    (response.value.data).forEach( ( item, index) => {
                        that.tableData.push({
                            TB_id: item.strainno,
                            TB_xueqing: item.serotype,
                            TB_mlst: item.strainmlst,
                            TB_plg: item.strainplg,
                            TB_remark: item.strainremarks,
                            TB_failReason: item.otherMsg,
                        })
                    });
                }else{
                    this.fileList = [];
                    this.$message({
                      message: response.value,
                      type: 'error'
                    });
                }
            }
        },
        failUpload(err, response, file){
            this.fileList = [];
            this.$message({
              message: err,
              type: 'error'
            });
        },
        goBack(){
            this.$router.push({ path: '/strainBasicInfo' })
        },
        downLoadFile(){
            window.open('/static/uploads/strain.xls')
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
   },
   watch:{
       switchNV(val){
           if(val){
              this.cotDivWidth = {
                   width: '1150px',
              }
          }else{
              this.cotDivWidth = {
                   width: '100%',
              }
          }
      },
   }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-top: 20px;
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
/*对话框*/
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



