<template>
  <div id="samSiteImpotDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="MIC信息-阳性"></my-location>
              <my-navigat activ="1-4"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                    <el-upload
                      action="//jsonplaceholder.typicode.com/posts/"
                      :on-preview="handlePreview"
                      :on-remove="handleRemove"
                      :default-file-list="fileList">
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div class="el-upload__tip" slot="tip">只能上传excel文件，excel的格式模板可点击下方按钮下载</div>
                    </el-upload>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button icon="d-arrow-left" @click="goBack">返回</el-button>
                    <el-button icon="caret-bottom" type="primary" @click="downLoadFile">下载导入模板</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                    <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%;"
                    max-height="600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      label="菌株编号"
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      label="内部编号"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_AMP"
                      label="AMP"
                      width="52">
                    </el-table-column>
                    <!-- <el-table-column
                      :prop="item"
                      :label="item"
                      width="52"
                      v-for="item in YangSeq">
                    </el-table-column> -->
                    <el-table-column
                      prop="TB_CIP"
                      label="CIP"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_CLI"
                      label="CLI"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_CQM"
                      label="CQM"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_ERY"
                      label="ERY"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_FFC"
                      label="FFC"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_FOX"
                      label="FOX"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_GEN"
                      label="GEN"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_LZD"
                      label="LZD"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_OXA"
                      label="OXA"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_PEN"
                      label="PEN"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_RIF"
                      label="RIF"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_TET"
                      label="TET"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_TIA"
                      label="TIA"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_VAL"
                      label="VAL"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_VAN"
                      label="VAN"
                      width="52">
                    </el-table-column>
                    <el-table-column
                      prop="TB_man"
                      label="实验人员"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_style"
                      label="实验方式"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="备注">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="MIC耐药性信息-阳性" v-model="dialogFormVisible" size="large">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="菌株编号" :label-width="formLabelWidth" prop="id">
                      <el-input v-model="formAdd.id" auto-complete="off"  :disabled="!currentOpert"></el-input>
                    </el-form-item>
                    <el-form-item label="实验人员" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="AMP" :label-width="formLabelWidth" prop="AMP">
                        <el-input v-model="formAdd.AMP" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CIP" :label-width="formLabelWidth" prop="CIP">
                        <el-input v-model="formAdd.CIP" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CLI" :label-width="formLabelWidth" prop="CLI">
                        <el-input v-model="formAdd.CLI" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CQM" :label-width="formLabelWidth" prop="CQM">
                        <el-input v-model="formAdd.CQM" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="ERY" :label-width="formLabelWidth" prop="ERY">
                        <el-input v-model="formAdd.ERY" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="FFC" :label-width="formLabelWidth" prop="FFC">
                        <el-input v-model="formAdd.FFC" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="FOX" :label-width="formLabelWidth" prop="FOX">
                        <el-input v-model="formAdd.FOX" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="GEN" :label-width="formLabelWidth" prop="GEN">
                        <el-input v-model="formAdd.GEN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="LZD" :label-width="formLabelWidth" prop="LZD">
                        <el-input v-model="formAdd.LZD" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="OXA" :label-width="formLabelWidth" prop="OXA">
                        <el-input v-model="formAdd.OXA" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="PEN" :label-width="formLabelWidth" prop="PEN">
                        <el-input v-model="formAdd.PEN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="RIF" :label-width="formLabelWidth" prop="RIF">
                        <el-input v-model="formAdd.RIF" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="TET" :label-width="formLabelWidth" prop="TET">
                        <el-input v-model="formAdd.TET" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="TIA" :label-width="formLabelWidth" prop="TIA">
                        <el-input v-model="formAdd.TIA" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="VAL" :label-width="formLabelWidth" prop="VAL">
                        <el-input v-model="formAdd.VAL" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="VAN" :label-width="formLabelWidth" prop="VAN">
                        <el-input v-model="formAdd.VAN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="实验方式" :label-width="formLabelWidth" prop="style">
                      <el-input v-model="formAdd.style" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="失败原因" :label-width="formLabelWidth" prop="fail">
                      <el-input v-model="formAdd.fail" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../../components/header'
import myLocation from './../../components/location'
import myNavigat from './../../components/navigat'
import axios from 'axios'
import dataSet from './../../publicAPI/dataSet'
import dataRules from './../../publicAPI/dataRules'
import config from './../../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data() {
      return {
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          currentOpert: true, //true是新增，false是修改
          formSamInfo: {
                ID: '',
                INNO: '',
                HIDECOL: [],
                DRUGRANGE: '',
                DRUGRANGE_S: '',
                DRUGRANGE_E: '',
          },
          add_range: [ //添加药物筛选范围
          ],
          formAdd: {
              id: '',
              inno: '',
              AMP: '',
              CIP: '',
              CLI: '',
              CQM: '',
              ERY: '',
              FFC: '',
              FOX: '',
              GEN: '',
              LZD: '',
              OXA: '',
              PEN: '',
              RIF: '',
              TET: '',
              TIA: '',
              VAL: '',
              VAN: '',
              man: '',
              style: '',
              remark: '',
          },
          formAddRules: {
              id: [
                  { required: true, message: '请输入菌株编号', trigger: 'blur' },
                  { min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }
              ]
          },
          switchSH: true,   //查询
          switchNV: true,   //导航
          loading2: false,
          currentPage: 1,
          pageSize: 100,
          totalPage: 0,
          formLabelWidth: '84px',
          cotDivWidth:{
              width: '1150px',
          },
          YangDrugSet: [],
          YangSeq: [],
          fileList: [
          ]
      };
  },
  methods:{
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        onEdit(){

        },
        onDelete(){

        },
        onConfirm(){

        },
        getList(){

        },
        goBack(){
            this.$router.push({ path: '/micInfoYang' })
        },
        downLoadFile(){

        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
   },
   watch:{
       switchNV(val){
           if(val){
              this.cotDivWidth = {
                   width: '1150px',
              }
          }else{
              this.cotDivWidth = {
                   width: '100%',
              }
          }
      },
   }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-top: 20px;
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
/*对话框*/
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



