<!-- 样品信息 | created by JanvierCheng -->
<template>
  <div id="sampleInfoDiv">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="样品信息"></my-location>
              <my-navigat activ="1-2"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="75px" :inline="true" class="demo-form-inline">
                      <el-form-item label="样品编号" prop="ID">
                          <el-input v-model="formSamInfo.ID" ></el-input>
                      </el-form-item>
                      <el-form-item label="采样省份" prop="PROVINCE">
                          <el-select  v-model="formSamInfo.PROVINCE" multiple placeholder="选择省份" >
                              <el-option :value="prvnc.name" v-for="prvnc in prvncs"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="采样地名"  prop="CLOCATE">
                          <el-input v-model="formSamInfo.CLOCATE"></el-input>
                      </el-form-item>
                      <el-form-item label="养殖场名" prop="YNAME">
                          <el-input v-model="formSamInfo.YNAME"></el-input>
                      </el-form-item>
                      <el-form-item label="日龄" prop="AGE">
                          <el-input v-model="formSamInfo.AGE"></el-input>
                      </el-form-item>
                      <el-form-item label="来源" prop="SOURCE">
                          <el-select  v-model="formSamInfo.SOURCE" multiple placeholder="选择来源">
                              <el-option :value="source.sourcename" v-for="source in sourSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="部位" prop="PART">
                          <el-select  v-model="formSamInfo.PART" multiple placeholder="选择部位">
                              <el-option :value="part.partno" :label="part.partname" v-for="part in partSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="动物" prop="ANIMAL">
                          <el-select  v-model="formSamInfo.ANIMAL" multiple placeholder="选择动物">
                              <el-option :value="animal.animalno" :label="animal.animalname" v-for="animal in animalSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="采集时间">
                          <el-date-picker type="date" placeholder="选择日期" v-model="formSamInfo.date1" :editable="false" style="width: 140px !important;"></el-date-picker>
                          -&nbsp;<el-date-picker type="date" placeholder="选择日期" v-model="formSamInfo.date2" :editable="false" style="width: 140px !important;"></el-date-picker>
                      </el-form-item>
                  </el-form>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                    <el-button-group>
                      <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                      <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                      <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                    </el-button-group>
                    <el-button-group>
                      <router-link to="/sampleInfo/samImpot"><el-button type="primary" icon="upload" style="border-radius: 5px 0 0 5px !important;">导入Excel</el-button></router-link>
                      <el-button type="primary" icon="d-caret" @click="onExport">导出Excel</el-button>
                    </el-button-group>

                    <el-button-group>
                      <el-button icon="search" @click="getList">搜索</el-button>
                      <el-button icon="close" @click="resetForm('formSamInfo')">重置</el-button>
                    </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height="600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      label="样品编号"
                      fixed
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_time"
                      label="采样时间"
                      width="90">
                    </el-table-column>
                    <el-table-column
                      prop="TB_provc"
                      label="采样省份"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_site"
                      label="采样地 "
                      width="248">
                    </el-table-column>
                    <el-table-column
                      prop="TB_yname"
                      label="养殖场"
                      width="260">
                    </el-table-column>
                    <el-table-column
                      prop="TB_animal"
                      label="动物"
                      width="65">
                    </el-table-column>
                    <el-table-column
                      prop="TB_age"
                      label="日龄"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_source"
                      label="来源"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_part"
                      label="部位"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_man"
                      label="采样人"
                      width="140">
                    </el-table-column>
                    <el-table-column
                      prop="TB_history"
                      label="动物用药史"
                      width="260">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      width="400"
                      label="备注">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="样品信息" v-model="dialogFormVisible" >
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="样品编号" :label-width="formLabelWidth" prop="id" v-if="!currentOpert">
                      <el-input v-model="formAdd.id" auto-complete="off"  :disabled="!currentOpert"></el-input>
                    </el-form-item>
                    <el-form-item label="采样时间" :label-width="formLabelWidth" prop="time" v-if="currentOpert">
                      <!-- <el-input v-model="formAdd.time" auto-complete="off"></el-input> -->
                      <el-date-picker type="date" placeholder="选择日期" v-model="formAdd.time" :editable="false"  :disabled="!currentOpert"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="采样时间" :label-width="formLabelWidth" prop="timeEdit" v-if="!currentOpert">
                      <!-- <el-input v-model="formAdd.time" auto-complete="off"></el-input> -->
                      <el-input placeholder="选择日期" v-model="formAdd.timeEdit"  auto-complete="off"  :disabled="!currentOpert"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="采样省份" :label-width="formLabelWidth" prop="province">
                      <!-- <el-input v-model="formAdd.province" auto-complete="off"></el-input> -->
                      <el-select  v-model="formAdd.province" placeholder="选择省份"  :disabled="!currentOpert">
                          <el-option :value="prvnc.name" v-for="prvnc in prvncs"></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item label="动物" :label-width="formLabelWidth" prop="animal">
                      <!-- <el-input v-model="formAdd.animal" auto-complete="off"></el-input> -->
                      <el-select  v-model="formAdd.animal"  placeholder="选择动物" :disabled="!currentOpert">
                          <el-option :value="animal.animalno" :label="animal.animalname" v-for="animal in animalSelect"></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item label="来源" :label-width="formLabelWidth" prop="source">
                      <!-- <el-input v-model="formAdd.source" auto-complete="off"></el-input> -->
                      <el-select  v-model="formAdd.source" placeholder="选择来源" :disabled="!currentOpert">
                          <el-option :value="source.sourcename" v-for="source in sourSelect"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="部位" :label-width="formLabelWidth" prop="part">
                      <!-- <el-input v-model="formAdd.part" auto-complete="off"></el-input> -->
                      <el-select  v-model="formAdd.part" placeholder="选择部位" :disabled="!currentOpert">
                          <el-option :value="part.partno" :label="part.partname" v-for="part in partSelect"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="采样地" :label-width="formLabelWidth" prop="clocate">
                      <!-- <el-input v-model="formAdd.clocate" auto-complete="off" :readonly="formAdd.province==''"></el-input> -->
                      <el-select  v-model="formAdd.clocate" placeholder="选择采样地" :disabled="formAdd.province==''">
                          <el-option :value="citem" v-for="citem in cLocateSelect"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="养殖场" :label-width="formLabelWidth" prop="yazhic">
                      <el-input v-model="formAdd.yazhic" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="日龄" :label-width="formLabelWidth" prop="age">
                      <el-input v-model="formAdd.age" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="采样人" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off" ></el-input>
                    </el-form-item>
                    <el-form-item label="动物用药史" :label-width="formLabelWidth" prop="history">
                      <el-input v-model="formAdd.history" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import dataSet from './../publicAPI/dataSet'
import axios from 'axios';
import config from './../publicAPI/config'
import dataRules from './../publicAPI/dataRules'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data(){
      return{
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          currentOpert: true, //true是新增，false是修改
          formSamInfo: {
                ID: '',
                PROVINCE: [],
                CLOCATE: '',
                YNAME: '',
                AGE: '',
                ANIMAL: [],
                SOURCE: [],
                PART: [],
                date1: '',
                date2: '',
          },
          formAdd: {
              id: '',
              time: '',
              timeEdit: '',
              province: '',
              clocate:'',
              yazhic: '',
              animal: '',
              age: '',
              source: '',
              part: '',
              man: '',
              history: '',
              remark: '',
              animal_id: '',
              part_id: '',
          },
          formAddRules: {
              time: [
                  { type: 'date' ,required: true, message: '请输入采样时间', trigger: 'change' },
              ],
              province: [
                  { required: true, message: '请选择省份', trigger: 'change' },
              ],
              clocate: [
                  { required: true, message: '请输入采样地', trigger: 'blur' },
              ],
              animal: [
                  { type: 'number', required: true, message: '请选择动物', trigger: 'change' },
              ],
              source: [
                  { required: true, message: '请选择来源', trigger: 'change' },
              ],
              part: [
                  { required: true, message: '请选择部位', trigger: 'change' },
              ],
          },
          switchSH: true,   //查询
          switchNV: true,   //导航
          loading2: false,
          currentPage: 1,
          pageSize: 100,
          totalPage: 0,
          formLabelWidth: '90px',
          cotDivWidth:{
              width: '1150px',
          },
          prvncs: dataSet.province,
          partSelect: [],
          animalSelect: [],
          sourSelect: [],
          cLocateSelect: []
      }
  },
  methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        onAdd(){
            this.dialogFormVisible = true
            this.currentOpert = true
        },
        onEdit(){
            if(this.multipleSelection.length==1){
                this.dialogFormVisible = true
                this.currentOpert = false
                this.formAdd = {
                    id: this.multipleSelection[0].TB_id,
                    timeEdit: this.multipleSelection[0].TB_time,
                    province: this.multipleSelection[0].TB_provc,
                    clocate:this.multipleSelection[0].TB_site,
                    yazhic: this.multipleSelection[0].TB_yname,
                    animal: this.multipleSelection[0].TB_animal_id,
                    age: this.multipleSelection[0].TB_age,
                    source: this.multipleSelection[0].TB_source,
                    part: this.multipleSelection[0].TB_part_id,
                    man: this.multipleSelection[0].TB_man,
                    history: this.multipleSelection[0].TB_history,
                    remark: this.multipleSelection[0].TB_history,
                }
            }
        },
        onDelete(){
            if(this.multipleSelection.length>0){
                let that = this;
                this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then(() => {
                    let str = "";
                    for(var item of this.multipleSelection){
                        str = str + item.TB_id + ','
                    }
                    axios.post(that.URL+'/sample/deletion', {
                       sampleno: str
                    })
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.getList();
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                    });

                }).catch((error) => {
                    // this.$message.error(error);
                });
            }
        },
        onExport(){
            let that = this;
            if((this.multipleSelection).length > 0){
                var sampleStr = '';
                for ( var item of this.multipleSelection){
                    sampleStr = sampleStr + item.TB_id + ','
                }
                console.log(sampleStr)
                axios.get(that.URL + '/sample/download?sampleno=' + sampleStr)
                    .then((res) => {
                        window.open(that.URL + '/sample/download?sampleno=' + sampleStr)
                    })
                    .catch((error) =>{
                        console.error(error);
                    })
            }else{
                this.$message.error('请选择要导出的行！');
            }

        },
        onConfirm(){
            let that = this
            var param = {
                // sampledateStr: dataRules.formatDate(this.formAdd.time),
                sampleprovince: this.formAdd.province,
                farmname: this.formAdd.clocate,
                samplefarmaddr: this.formAdd.yazhic,
                animalno: this.formAdd.animal,
                sampleanimalage: this.formAdd.age,
                samplesource: this.formAdd.source,
                samplecollectionpart: this.formAdd.part,
                samplecollector: this.formAdd.man,
                samplemedicalhistory: this.formAdd.history,
                sampleremarks: this.formAdd.remark,
            }
// console.log(param)
            this.$refs['formAdd'].validate((valid) => {
                if (valid) {
                    if( this.currentOpert){//新增
                        param.sampledateStr = dataRules.formatDate(that.formAdd.time)
                        console.log('样品信息新增：'+param)
                        axios.post(that.URL+'/sample/addition', param)
                        .then(function (res) {
                            if(res.data.state){
                                that.$message({
                                  type: 'success',
                                  message: res.data.value
                                });
                                that.dialogFormVisible = false
                                that.getList()
                            }else{
                                that.$message({
                                  type: 'error',
                                  message: res.data.value
                                });
                            }
                        })
                        .catch(function (error) {
                          console.error(error);
                        });
                        this.dialogFormVisible = false
                    }else{//修改
                        param.sampledateStr = that.formAdd.time
                        param.sampleno = that.formAdd.id
                        axios.post(that.URL+'/sample/update', param)
                        .then(function (res) {
                            if(res.data.state){
                                that.$message({
                                  type: 'success',
                                  message: res.data.value
                                });
                                that.dialogFormVisible = false
                                that.getList()
                            }else{
                                that.$message({
                                  type: 'error',
                                  message: res.data.value
                                });
                            }
                        })
                        .catch(function (error) {
                          console.error(error);
                        });
                        this.dialogFormVisible = false
                    }
                } else {
                  console.log('error submit!!');
                  return false;
                }
            });
        },
        getCLocateList(){   //对话框中的采样地下拉
            let that = this
            console.warn(typeof(that.formAdd.province))
            axios.post(that.URL + '/farm/selection', {
                    farmprovince:that.formAdd.province
                })
                .then((res) => {
                    that.cLocateSelect = []
                    that.cLocateSelect = res.data.value
                })
                .catch((error) => {
                    console.error(error);
                })
        },
        getSelect(){    //获取下拉框
            let that = this
            axios.all([axios.get(that.URL + '/part/selection'), axios.get(that.URL + '/animal/selection'), axios.get(that.URL + '/source/selection')])
                .then(axios.spread(function (respart, resanimal, ressource) {
                    if(respart.data.state){
                        for(var item of respart.data.value){
                            if(item.partname == ""){
                                that.partSelect.push({
                                    partno: item.partno,
                                    partname: " "
                                })
                            }else{
                                that.partSelect.push({
                                    partno: item.partno,
                                    partname: item.partname
                                })
                            }
                        }
                    }
                    if(resanimal.data.state){
                        that.animalSelect = resanimal.data.value
                    }
                    if(ressource.data.state){
                        that.sourSelect = ressource.data.value
                    }
                }))
        },
        getList(){
            let that = this;
            this.loading2 = true
            var param = {
                sampleno: that.formSamInfo.ID,
                sampleprovince: that.formSamInfo.PROVINCE,
                farmname: that.formSamInfo.CLOCATE,
                samplefarmaddr: that.formSamInfo.YNAME,
                sampleanimalage: that.formSamInfo.AGE,
                samplesource: that.formSamInfo.SOURCE,
                samplecollectionpart: that.formSamInfo.PART,
                animalno: that.formSamInfo.ANIMAL,
                page: that.currentPage,
                beginDate: that.formSamInfo.date1,
                endDate: that.formSamInfo.date2,
                rows: that.pageSize,
                sort: '',
                order: '',
            }
            // console.log(JSON.stringify(param))

            axios.post(that.URL+'/sample/list', param)
            .then(function (res) {
                that.tableData = [];
                that.totalPage = res.data.value.pages
                for( var item of res.data.value.data){
                    that.tableData.push({
                        TB_id: item.sampleno,
                        TB_time: item.sampledateStr,
                        TB_provc: item.sampleprovince,
                        TB_site: item.farmname,
                        TB_yname: item.samplefarmaddr,
                        TB_animal: item.animalName,
                        TB_animal_id: item.animalno,
                        TB_age: item.sampleanimalage,
                        TB_source: item.samplesource,
                        TB_part: item.partName,
                        TB_part_id: item.samplecollectionpart,
                        TB_man: item.samplecollector,
                        TB_history: item.samplemedicalhistory,
                        TB_remark: item.sampleremarks,
                    })
                }
                that.loading2 = false;
            })
            .catch(function (error) {
              console.error(error);
            });
        },
        resetForm(formName){
            this.$refs[formName].resetFields();
            if(formName == 'formSamInfo'){
                this.formSamInfo.date1 = "";
                this.formSamInfo.date2 = "";
                this.getList();
            }else if(formName == 'formAdd'){
                this.formAdd =  {
                    id: '',
                    time: '',
                    timeEdit: '',
                    province: '',
                    clocate:'',
                    yazhic: '',
                    animal: '',
                    age: '',
                    source: '',
                    part: '',
                    man: '',
                    history: '',
                    remark: '',
                    animal_id: '',
                    part_id: '',
                }
                this.dialogFormVisible = false
            }
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
  },
  created(){
    this.getList();
    this.getSelect();
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
     },
    //  dialogFormVisible(){
    //      if(!this.dialogFormVisible){
    //          this.$refs['formAdd'].resetFields();
    //      }
    //  },
    'formAdd.province': function(_new,_old){
      //   console.log(_old+'----'+_new)
          if(_old != ""){
              this.formAdd.clocate = "";
              this.getCLocateList();
          }else{
              this.getCLocateList();
          }

    }
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
/*表单输入组*/
.inputGp{
    margin-bottom: 20px;
}
.el-form-item{
    margin-bottom: 4px;
}
.inputGp .el-input,.inputGp .el-select,.inputGp .el-date-editor{
    width: 224px !important;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



