<template>
  <div id="analyseDrugDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="耐药性分析-全部"></my-location>
              <my-navigat activ="2-2"></my-navigat>
          </div>
          <div class="ho-cotet">
              <div class="inputG">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="90px" :inline="true" class="demo-form-inline">
                      <el-form-item label="药物" prop="allDrug">
                          <el-select  v-model="formSamInfo.allDrug" multiple placeholder="选择药物" style="width: 560px;" >
                              <el-option :value="item" v-for="item in drugSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="类型统计" prop="typeAnalyse">
                          <el-select  v-model="formSamInfo.typeAnalyse" placeholder="选择类型统计">
                              <el-option label="按动物统计" value="1"></el-option>
                              <el-option label="按采样地统计" value="2"></el-option>
                              <el-option label="按来源统计" value="3"></el-option>
                              <el-option label="按省份统计" value="4"></el-option>
                              <el-option label="按动物大类统计" value="5"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="菌种名" prop="strainname">
                          <el-select  v-model="formSamInfo.strainname" placeholder="选择菌种" style="width: 560px;">
                              <el-option :value="item" v-for="item in strainSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="时间方式" prop="timeStyle">
                          <el-select  v-model="formSamInfo.timeStyle" placeholder="选择时间方式">
                              <el-option label="按年统计" value="1"></el-option>
                              <el-option label="按半年统计" value="3"></el-option>
                              <el-option label="按季度统计" value="4"></el-option>
                              <el-option label="按月统计" value="2"></el-option>
                          </el-select>
                      </el-form-item>
                  </el-form>
              </div>
              <div class="youVeSelect">
                  <h4><b>菌株编号</b> <el-tag type="primary" v-if="selectInfo.strainno!=''"> {{ selectInfo.strainno }} </el-tag> </h4>
                  <h4><b>内部编号</b> <el-tag type="primary" v-if="selectInfo.inno!=''"> {{ selectInfo.inno }} </el-tag> </h4>
                  <h4><b>省份：</b> <el-tag type="primary" v-for="province in selectInfo.province">{{ province }} </el-tag> </h4>
                  <h4><b>省份：</b> <el-tag type="primary" v-for="province in selectInfo.province">{{ province }} </el-tag> </h4>
                  <h4><b>动物：</b> <el-tag type="primary" v-for="animal in selectInfo.animal">{{ animal }} </el-tag> </h4>
                  <h4><b>开始时间：</b> <el-tag type="primary" v-if="selectInfo.date1!=''"> {{ selectInfo.date1 }} </el-tag> </h4>
                  <h4><b>结束时间：</b> <el-tag type="primary" v-if="selectInfo.date2!=''"> {{ selectInfo.date2 }} </el-tag> </h4>
                  <h4><b>采样地名：</b> <el-tag type="primary" v-if="selectInfo.caiyangdi.length>0"> {{ selectInfo.caiyangdi }} </el-tag> </h4>
                  <h4><b>养殖场名：</b> <el-tag type="primary" v-if="selectInfo.yangzhi.length>0"> {{ selectInfo.yangzhi }} </el-tag> </h4>
                  <h4><b>日龄：</b> <el-tag type="primary" v-if="selectInfo.age!=''"> {{ selectInfo.age }} </el-tag> </h4>
                  <h4><b>菌属</b> <el-tag type="primary" v-for="junshu in selectInfo.junshu"> {{ junshu }} </el-tag> </h4>
                  <h4><b>来源：</b> <el-tag type="primary" v-for="source in selectInfo.source">{{ source }} </el-tag> </h4>
                  <h4><b>部位：</b> <el-tag type="primary" v-for="part in selectInfo.part">{{ part }} </el-tag> </h4>
                  <h4><b>药品查找：</b> <el-tag type="primary" v-for="range in selectInfo.add_range">{{ range.S }}  ≤ {{ range.R }} ≤  {{ range.E }} </el-tag> </h4>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button icon="d-arrow-left" @click="toBack">返回耐药性分析</el-button>
                    <el-button type="primary" icon="date" @click="makeChart" :loading="loadingBtn">生成报表</el-button>
                  </el-button-group>
              </div>
          </div>
          <div class="clearfix"></div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import axios from 'axios'
import dataSet from './../publicAPI/dataSet'
import config from './../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data() {
      return {
          URL: config.rootURL,
          selectInfo: {
              strainno: '',
              inno: '',
              province: [],
              animal: [],
              date1: '',
              date2: '',
              caiyangdi: '',
              yangzhi: '',
              age: '',
              source: [],
              part: [],
              junshu: [],
              add_range: [],
          },
          formSamInfo: {
              allDrug: [],
              strainname: '',
              typeAnalyse: '',
              timeStyle: '',
          },
          drugSelect: dataSet.allDrug,
          strainSelect: [],
          loadingBtn: false,
      };
  },
  methods:{
      toBack(){
          this.$router.push({path: '/drugResistance'})
      },
      makeChart(){
          let that = this;
          that.loadingBtn = true
          var typeAnalyse = "";
          if( this.formSamInfo.typeAnalyse == '1' ){
              typeAnalyse = '动物';
          }else if( this.formSamInfo.typeAnalyse == '2' ){
              typeAnalyse = '采样地';
          }else if( this.formSamInfo.typeAnalyse == '3' ){
              typeAnalyse = '来源';
          }else if( this.formSamInfo.typeAnalyse == '4' ){
              typeAnalyse = '省份';
          }else if( this.formSamInfo.typeAnalyse == '5' ){
              typeAnalyse = '动物大类';
          }
          localStorage.setItem('ResisType', typeAnalyse ); //按什么统计
          localStorage.setItem('ResisDrugArr', JSON.stringify(that.formSamInfo.allDrug) );//药物——数组
          var drugStr = "";
          for( var item of this.formSamInfo.allDrug){
              drugStr = drugStr + item + ',';
          }
           localStorage.setItem('ResisDrug', drugStr );//药物——字符串
          var makeChartObj = {
              strainno: this.selectInfo.strainno,
              strainalias: this.selectInfo.inno,
              animalAge: this.selectInfo.age,
              animalName: this.selectInfo.animal,
              beginDate: this.selectInfo.date1,
              category: this.selectInfo.junshu,
              endDate: this.selectInfo.date2,
              farmaddr: this.selectInfo.caiyangdi,
              farmname: this.selectInfo.yangzhi,
              partName: this.selectInfo.part,
              province: this.selectInfo.province,
              sampleno: this.selectInfo.sampleno,
              source: this.selectInfo.source,
              statisticsType: this.formSamInfo.typeAnalyse,
              strain: that.formSamInfo.strainname,
              drug: drugStr,
              timeWay: this.formSamInfo.timeStyle,
          }
          for( var item of that.selectInfo.add_range){
              makeChartObj[(item.R).toLowerCase()+'1'] = parseFloat(item.S)
              makeChartObj[(item.R).toLowerCase()+'2'] = parseFloat(item.E)
          }
          console.warn(JSON.stringify(makeChartObj));
          axios.post(that.URL + '/druganalysis/all/analysis', makeChartObj)
            .then((res) => {
                if(res.data.state){
                    localStorage.setItem('ResisChart',JSON.stringify(res.data.value));

                    this.$router.push({ path: '/analyseDrugResis/anlysAll' })
                }else{
                    this.$message({
                      showClose: true,
                      message: res.data.value,
                      type: 'error'
                    });
                }
                that.loadingBtn = false
            })
            .catch((error) => {
                console.error(error);
            })
      }
  },
  created(){
      let that = this
      axios.get(that.URL + '/standardmic/strain/selection')
        .then((res) => {
            that.strainSelect = res.data.value
        })
        .catch((error) => {
            console.error(error);
        })

      var obj = JSON.parse(localStorage.getItem('drugResistance'))
      console.log(localStorage.getItem('drugResistance'))
      this.selectInfo.province = obj.province;
      this.selectInfo.animal = obj.animal;
      this.selectInfo.date1 = obj.date1;
      this.selectInfo.date2 = obj.date2;
      this.selectInfo.caiyangdi = obj.caiyangdi;
      this.selectInfo.yangzhi = obj.yangzhi;
      this.selectInfo.age = obj.age;
      this.selectInfo.source = obj.source;
      this.selectInfo.junshu = obj.junshu;
      this.selectInfo.strainno = obj.id;
      this.selectInfo.inno = obj.inno;
      this.selectInfo.add_range = obj.addRange;
      for( var item of obj.part){
          if(item == " "){
              this.selectInfo.part.push('（空）');
          }else{
              this.selectInfo.part.push(item);
          }
      }
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
.youVeSelect{
    padding-left: 80px;
}
.youVeSelect h4{
    font-weight: normal;
    color: #475669;
    font-size: 14px;
    line-height: 34px;
}
.youVeSelect b{
    color: #1D8CE0;
    font-weight: normal;
    display: inline-block;
    width: 90px;
}
.operatBtnG{
    margin-top: 26px;
    padding-left: 70px;
}
.el-tag{
    margin-right: 10px;
    text-align: center;
    font-size: 14px;
}
</style>



