<!-- 首页 | created by JanvierCheng -->
<template>
  <div id="drugInfoDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav">
              <my-location title="药品标准信息"></my-location>
              <my-navigat activ="3-1"></my-navigat>
          </div>
          <div class="ho-cotet">
              <div class="inputGp">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="70px" :inline="true" class="demo-form-inline">
                      <el-form-item label="标准名称" prop="SNAME">
                          <el-input v-model="formSamInfo.SNAME" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="药品名称" prop="DNAME">
                          <el-input v-model="formSamInfo.DNAME" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="细菌" prop="STRAIN">
                          <el-input v-model="formSamInfo.STRAIN" size="small"></el-input>
                      </el-form-item>
                  </el-form>
              </div>
              <div>
                  <el-button-group>
                    <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                    <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                    <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button icon="search" @click="getList">搜索</el-button>
                    <el-button icon="close" @click="resetForm('formSamInfo')">重置</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height="600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_sname"
                      label="标准名称">
                    </el-table-column>
                    <el-table-column
                      prop="TB_dname"
                      label="药品名称">
                    </el-table-column>
                    <el-table-column
                      prop="TB_strain"
                      label="细菌">
                    </el-table-column>
                    <el-table-column
                      prop="TB_zhedian"
                      label="折点">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="备注">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[10, 20, 30, 40]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="用户信息" v-model="dialogFormVisible">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd">
                    <el-form-item label="标准名称" :label-width="formLabelWidth" prop="sname">
                      <el-input v-model="formAdd.sname" auto-complete="off" :disabled="!currentOpert"></el-input>
                    </el-form-item>
                    <el-form-item label="药品名称" :label-width="formLabelWidth" prop="dname">
                      <el-input v-model="formAdd.dname" auto-complete="off" :disabled="!currentOpert"></el-input>
                    </el-form-item>
                    <el-form-item label="细菌" :label-width="formLabelWidth" prop="strain">
                      <el-input v-model="formAdd.strain" auto-complete="off" :disabled="!currentOpert"></el-input>
                    </el-form-item>
                    <el-form-item label="折点" :label-width="formLabelWidth" prop="zhedian">
                      <el-input v-model="formAdd.zhedian" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>

  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import axios from 'axios'
import config from './../publicAPI/config'

export default {
  // name: 'login',
  components: {
      myHeader,
      myLocation,
      myNavigat
  },
  data(){
      return {
          URL: config.rootURL,
          formSamInfo: {
              SNAME: '',
              DNAME: '',
              STRAIN: ''
          },
          tableData: [{},{},{}],
          multipleSelection: [],
          dialogFormVisible: false,
          currentOpert: true, //true是新增，false是修改
          formAdd: {
              dname: '',
              sname: '',
              strain: '',
              zhedian: '',
              remark: ''
          },
          formAddRules:{
              dname: [
                  { required: true, message: '请输入标准名称', trigger: 'blur' },
                  { min: 1, max: 30, message: '长度在 1 到 30 个字符', trigger: 'blur' }
              ],
              sname: [
                  { required: true, message: '请输入药品名称', trigger: 'blur' },
                  { min: 1, max: 30, message: '长度在 1 到 30 个字符', trigger: 'blur' }
              ],
              strain: [
                  { required: true, message: '请输入细菌', trigger: 'blur' },
                  { min: 1, max: 30, message: '长度在 1 到 30 个字符', trigger: 'blur' }
              ],
          },
          loading2: true,
          currentPage: 1,
          pageSize: 10,
          totalPage: 0,
          formLabelWidth: '120px'
      }
  },
  methods:{
      onAdd(){
          this.dialogFormVisible = true
          this.currentOpert = true
      },
      onEdit(){
          if(this.multipleSelection.length==1){
              this.dialogFormVisible = true
              this.currentOpert = false
              this.formAdd = {
                  dname: this.multipleSelection[0].TB_dname,
                  sname: this.multipleSelection[0].TB_sname,
                  strain: this.multipleSelection[0].TB_strain,
                  zhedian: this.multipleSelection[0].TB_zhedian,
                  remark: this.multipleSelection[0].TB_remark,
              }
          }
      },
      onDelete(){
          if(this.multipleSelection.length>0){
              let that = this;
              this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                  let array = [];
                  for(var item of this.multipleSelection){
                      array.push({
                          medicalname: item.TB_dname,
                          standardname: item.TB_sname,
                          strainname: item.TB_strain
                      })
                  }
                  axios.post(that.URL+'/standardmic/deletion', array)
                  .then(function (res) {
                      if(res.data.state){
                          that.$message({
                            type: 'success',
                            message: res.data.value
                          });
                          that.getList();
                      }else{
                          that.$message({
                            type: 'error',
                            message: res.data.value
                          });
                      }
                  })
                  .catch(function (error) {
                  });

              }).catch((error) => {
                  // this.$message.error(error);
              });
          }
      },
      onConfirm(){
          let that = this
          var param = {
              medicalname: this.formAdd.dname,
              standardname: this.formAdd.sname,
              strainname: this.formAdd.strain,
              medlimit: parseInt(this.formAdd.zhedian),
              remark: this.formAdd.remark,
          }
          this.$refs['formAdd'].validate((valid) => {
              if (valid) {
                  if( this.currentOpert){//新增
                      axios.post(that.URL+'/standardmic/addition', param)
                      .then(function (res) {
                          if(res.data.state){
                              that.$message({
                                type: 'success',
                                message: res.data.value
                              });
                              that.dialogFormVisible = false
                              that.getList()
                          }else{
                              that.$message({
                                type: 'error',
                                message: res.data.value
                              });
                          }
                      })
                      .catch(function (error) {
                        console.error(error);
                      });
                      this.dialogFormVisible = false
                  }else{//修改
                      axios.post(that.URL+'/standardmic/update', param)
                      .then(function (res) {
                          if(res.data.state){
                              that.$message({
                                type: 'success',
                                message: res.data.value
                              });
                              that.dialogFormVisible = false
                              that.getList()
                          }else{
                              that.$message({
                                type: 'error',
                                message: res.data.value
                              });
                          }
                      })
                      .catch(function (error) {
                        console.error(error);
                      });
                      this.dialogFormVisible = false
                  }
              } else {
                console.log('error submit!!');
                return false;
              }
          });
      },
      getList(){
          let that = this;
          var param = {
              page: that.currentPage,
              rows: that.pageSize,
              standardname: that.formSamInfo.SNAME,
              medicalname: that.formSamInfo.DNAME,
              strainname: that.formSamInfo.STRAIN,
          }
          axios.post(that.URL+'/standardmic/list', param)
          .then(function (res) {
              that.tableData = [];
              that.totalPage = res.data.value.pages
              for( var item of res.data.value.data){
                  that.tableData.push({
                      TB_dname: item.medicalname,
                      TB_sname: item.standardname,
                      TB_strain: item.strainname,
                      TB_zhedian: item.medlimit,
                      TB_remark: item.remark,
                  })
              }
              that.loading2 = false;
          })
          .catch(function (error) {
            console.error(error);
          });
      },
      resetForm(formName){
          this.$refs[formName].resetFields();
          if(formName == 'formSamInfo'){
              this.getList();
          }else if(formName == 'formAdd'){
              this.formAdd =  {
                  dname: '',
                  sname: '',
                  strain: '',
                  zhedian: '',
                  remark: ''
              }
              this.dialogFormVisible = false
          }
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
        //   console.log(this.multipleSelection);
      },
      handleSizeChange(val) {
          this.pageSize = val;
          this.getList();
          console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
          this.currentPage = val;
          this.getList();
          console.log(`当前页: ${val}`);
      },
  },
  created(){
      this.getList();
  },
  watch:{
    //   'dialogFormVisible':function(){
    //       if(!this.dialogFormVisible){
    //           this.$refs['formAdd'].resetFields();
    //       }
    //   }
  }
}
</script>

<style scoped>
.el-button-group{
    margin-bottom: 30px;
}
.inputGp{
    margin-bottom: 20px;
}
.inputGp .el-input{
    width: 240px;
}
.el-form-item{
    margin-bottom: 0 !important;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
.block{
    margin-top: 30px;
}
</style>



