<template>
  <div id="geneImpotDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="耐药性基因信息"></my-location>
              <my-navigat activ="1-6"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                    <el-upload
                      action="//jsonplaceholder.typicode.com/posts/"
                      :on-preview="handlePreview"
                      :on-remove="handleRemove"
                      :default-file-list="fileList">
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div class="el-upload__tip" slot="tip">只能上传excel文件，excel的格式模板可点击下方按钮下载</div>
                    </el-upload>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button icon="d-arrow-left" @click="goBack">返回</el-button>
                    <el-button icon="caret-bottom" type="primary" @click="downLoadFile">下载导入模板</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                    <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height = "600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35" >
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      label="菌株编号"
                      width="200">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      label="内部编号"
                      width="140">
                    </el-table-column>
                    <el-table-column
                      prop="TB_gene"
                      label="耐药基因"
                      width="198">
                    </el-table-column>
                    <el-table-column
                      prop="TB_charu"
                      label="插入元件"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_copy"
                      label="复制子"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_jiehe"
                      label="接合子"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_man"
                      label="操作人"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="备注">
                    </el-table-column>
                    <el-table-column
                      prop="TB_fail"
                      label="失败原因">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="采样地信息" v-model="dialogFormVisible">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="菌株编号" :label-width="formLabelWidth" prop="id">
                      <el-input v-model="formAdd.id" auto-complete="off" :disabled="!currentOper"></el-input>
                    </el-form-item>
                    <el-form-item label="内部编号" :label-width="formLabelWidth" prop="inno">
                      <el-input v-model="formAdd.inno" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="耐药基因" :label-width="formLabelWidth" prop="gene">
                      <el-input v-model="formAdd.gene" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="插入元件" :label-width="formLabelWidth" prop="charu">
                      <el-input v-model="formAdd.charu" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="复制子" :label-width="formLabelWidth" prop="copy">
                      <el-input v-model="formAdd.copy" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="结合子" :label-width="formLabelWidth" prop="jiehe">
                      <el-input v-model="formAdd.jiehe" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="操作人" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../../components/header'
import myLocation from './../../components/location'
import myNavigat from './../../components/navigat'
import axios from 'axios'
import dataSet from './../../publicAPI/dataSet'
import dataRules from './../../publicAPI/dataRules'
import config from './../../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data() {
      return {
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          formSamInfo: {
              ID: '',
              INNO: '',
              GENE: '',
              CHARU: '',
              COPY: '',
              MAN: ''
          },
          formAdd:{
              id: '',
              inno: '',
              gene: '',
              charu: '',
              copy: '',
              jiehe: '',
              man: '',
              remark: '',
          },
          formAddRules:{
              id: [
                   { required: true, message: '请输入菌株编号', trigger: 'blur' },
                   { min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }
              ],
          },
          switchSH: true,   //查询
          switchNV: true,   //导航
          cotDivWidth:{
              width: '1150px',
          },
          totalPage: 0, //总页数
          pageSize: 100, //每页记录数
          loading2: false,    //表格加载中
          dialogFormVisible: false,
          formLabelWidth: '85px',
          currentPage: 1,   //当前页码
          currentOper: true,  //添加是true,修改是false
          fileList: [
          ]
      };
  },
  methods:{
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        onEdit(){

        },
        onDelete(){

        },
        onConfirm(){

        },
        getList(){

        },
        goBack(){
            this.$router.push({ path: '/geneBasicInfo' })
        },
        downLoadFile(){

        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
   },
   watch:{
       switchNV(val){
           if(val){
              this.cotDivWidth = {
                   width: '1150px',
              }
          }else{
              this.cotDivWidth = {
                   width: '100%',
              }
          }
      },
   }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-top: 20px;
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
/*对话框*/
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



