<!-- MIC信息（阴性） | created by JanvierCheng -->
<template>
  <div id="micInfoYangDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="MIC信息-阴性"></my-location>
              <my-navigat activ="1-5"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="68px" :inline="true" class="demo-form-inline">
                      <el-form-item label="菌株编号">
                          <el-input v-model="formSamInfo.ID"></el-input>
                      </el-form-item>
                      <el-form-item label="内部编号">
                          <el-input v-model="formSamInfo.INNO"></el-input>
                      </el-form-item>
                      <!-- <el-form-item label="隐藏列显示">
                          <el-select  v-model="formSamInfo.HIDECOL" multiple placeholder="选择隐藏列">
                              <el-option :value="ydrug" v-for="ydrug in YinDrugSet"></el-option>
                          </el-select>
                      </el-form-item> -->
                      <el-form-item label="药品查找" prop="DRUGRANGE">
                          <el-select  v-model="formSamInfo.DRUGRANGE" placeholder="选药品" style="width:120px !important;">
                              <el-option :value="ydrug" v-for="ydrug in YinDrugSet"></el-option>
                          </el-select>
                          &nbsp;从&nbsp;
                          <el-input v-model="formSamInfo.DRUGRANGE_S" style="width:56px !important;"></el-input>
                          &nbsp;到&nbsp;
                          <el-input v-model="formSamInfo.DRUGRANGE_E" style="width:56px !important;"></el-input>
                          <el-button icon="plus" @click="addRange">添加范围</el-button>
                      </el-form-item>
                  </el-form>
                  <myRange :items="add_range"></myRange>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                    <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                    <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                  </el-button-group>
                  <el-button-group>
                    <router-link to="/micInfoYin/micYinImpot"><el-button type="primary" icon="upload" style="border-radius: 5px 0 0 5px !important;">导入Excel</el-button></router-link>
                    <el-button type="primary" icon="d-caret">导出Excel</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button icon="search" @click="getList">搜索</el-button>
                    <el-button icon="close" @click="resetForm('formSamInfo')">重置</el-button>
                  </el-button-group>
              </div>
              <!-- <div class="tableBox"> -->
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%;"
                    max-height="600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      fixed
                      label="菌株编号"
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      fixed
                      label="内部编号"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      :prop="item"
                      :label="item"
                      width="52"
                      v-for="item in YinSeq">
                    </el-table-column>
                    <el-table-column
                      prop="TB_man"
                      label="实验人员"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_style"
                      label="实验方式">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="备注">
                    </el-table-column>
                  </el-table>
              </div>
              <!-- </div> -->
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="MIC耐药性信息-阴性" v-model="dialogFormVisible" size="large">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="菌株编号" :label-width="formLabelWidth" prop="id">
                      <el-input v-model="formAdd.id" auto-complete="off"  :disabled="!currentOpert"></el-input>
                    </el-form-item>
                    <el-form-item label="实验人员" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="AMK" :label-width="formLabelWidth" prop="AMK">
                        <el-input v-model="formAdd.AMK" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="AMP" :label-width="formLabelWidth" prop="AMP">
                        <el-input v-model="formAdd.AMP" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="APR" :label-width="formLabelWidth" prop="APR">
                        <el-input v-model="formAdd.APR" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CAZ" :label-width="formLabelWidth" prop="CAZ">
                        <el-input v-model="formAdd.CAZ" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CL" :label-width="formLabelWidth" prop="CL">
                        <el-input v-model="formAdd.CL" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CHL" :label-width="formLabelWidth" prop="CHL">
                        <el-input v-model="formAdd.CHL" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CIP" :label-width="formLabelWidth" prop="CIP">
                        <el-input v-model="formAdd.CIP" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CTX" :label-width="formLabelWidth" prop="CTX">
                        <el-input v-model="formAdd.CTX" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="CQM" :label-width="formLabelWidth" prop="CQM">
                        <el-input v-model="formAdd.CQM" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="DOX" :label-width="formLabelWidth" prop="DOX">
                        <el-input v-model="formAdd.DOX" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="FOX" :label-width="formLabelWidth" prop="FOX">
                        <el-input v-model="formAdd.FOX" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="FFC" :label-width="formLabelWidth" prop="FFC">
                        <el-input v-model="formAdd.FFC" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="FOS" :label-width="formLabelWidth" prop="FOS">
                        <el-input v-model="formAdd.FOS" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="GEN" :label-width="formLabelWidth" prop="GEN">
                        <el-input v-model="formAdd.GEN" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="IMP" :label-width="formLabelWidth" prop="IMP">
                        <el-input v-model="formAdd.IMP" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="OQX" :label-width="formLabelWidth" prop="OQX">
                        <el-input v-model="formAdd.OQX" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="NEO" :label-width="formLabelWidth" prop="NEO">
                        <el-input v-model="formAdd.NEO" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="SXT" :label-width="formLabelWidth" prop="SXT">
                        <el-input v-model="formAdd.SXT" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="STR" :label-width="formLabelWidth" prop="STR">
                        <el-input v-model="formAdd.STR" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="TET" :label-width="formLabelWidth" prop="TET">
                        <el-input v-model="formAdd.TET" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="MEM" :label-width="formLabelWidth" prop="MEM">
                        <el-input v-model="formAdd.MEM" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="实验方式" :label-width="formLabelWidth" prop="style">
                      <el-input v-model="formAdd.style" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import dataSet from './../publicAPI/dataSet'
import dataRules from './../publicAPI/dataRules'
import myRange from './../components/addDrugRange'
import axios from 'axios';
import config from './../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
      myRange
  },
  data(){
      return{
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          currentOpert: true, //true是新增，false是修改
          multipleSelection: [],
          formSamInfo: {
                ID: '',
                INNO: '',
                DRUGRANGE: '',
                DRUGRANGE_S: '',
                DRUGRANGE_E: '',
          },
          add_range: [ //添加药物筛选范围
          ],
          formAdd: {
              id: '',
              inno: '',
              AMK: '',
              AMP: '',
              APR: '',
              CAZ: '',
              CL: '',
              CHL: '',
              CIP: '',
              CTX: '',
              CQM: '',
              DOX: '',
              FOX: '',
              FFC: '',
              FOS: '',
              GEN: '',
              IMP: '',
              OQX: '',
              NEO: '',
              SXT: '',
              STR: '',
              TET: '',
              MEM: '',
              man: '',
              style: '',
              remark: '',
          },
          formAddRules: {
              id: [
                  { required: true, message: '请输入菌株编号', trigger: 'blur' },
                  { min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' }
              ]
          },
          switchSH: true,   //查询
          switchNV: true,   //导航
          loading2: false,
          currentPage: 1,
          pageSize: 100,
          totalPage: 0,
          formLabelWidth: '90px',
          cotDivWidth:{
              width: '1150px',
          },
          YinDrugSet: [],
          YinSeq: [],
      }
  },
  methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        onAdd(){
            this.dialogFormVisible = true
            this.currentOpert = true
        },
        onEdit(){
            if(this.multipleSelection.length==1){
                this.dialogFormVisible = true
                this.currentOpert = false
                this.formAdd = {
                    id: this.multipleSelection[0].TB_id,
                    AMK: this.multipleSelection[0].AMK,
                    AMP: this.multipleSelection[0].AMP,
                    APR: this.multipleSelection[0].APR,
                    CAZ: this.multipleSelection[0].CAZ,
                    CL: this.multipleSelection[0].CL,
                    CHL: this.multipleSelection[0].CHL,
                    CIP: this.multipleSelection[0].CIP,
                    CTX: this.multipleSelection[0].CTX,
                    CQM: this.multipleSelection[0].CQM,
                    DOX: this.multipleSelection[0].DOX,
                    FOX: this.multipleSelection[0].FOX,
                    FFC: this.multipleSelection[0].FFC,
                    FOS: this.multipleSelection[0].FOS,
                    GEN: this.multipleSelection[0].GEN,
                    IMP: this.multipleSelection[0].IMP,
                    OQX: this.multipleSelection[0].OQX,
                    NEO: this.multipleSelection[0].NEO,
                    SXT: this.multipleSelection[0].SXT,
                    STR: this.multipleSelection[0].STR,
                    TET: this.multipleSelection[0].TET,
                    MEM: this.multipleSelection[0].MEM,
                    man: this.multipleSelection[0].TB_man,
                    style: this.multipleSelection[0].TB_style,
                    remark: this.multipleSelection[0].TB_remark,
                }
            }
        },
        onDelete(){
            if(this.multipleSelection.length>0){
                let that = this;
                this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then(() => {
                    let str = "";
                    for(var item of this.multipleSelection){
                        str = str + item.TB_id + ','
                    }
                    axios.post(that.URL+'/mic/negative/deletion', {
                       strainno: str
                    })
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.getList();
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                    });

                }).catch((error) => {
                    // this.$message.error(error);
                });
            }
        },
        onConfirm(){
            let that = this
            var param = {
                strainno: this.formAdd.id,
                amk: parseFloat(this.formAdd.AMK),
                amp: parseFloat(this.formAdd.AMP),
                apr: parseFloat(this.formAdd.APR),
                caz: parseFloat(this.formAdd.CAZ),
                cl: parseFloat(this.formAdd.CL),
                chl: parseFloat(this.formAdd.CHL),
                cip: parseFloat(this.formAdd.CIP),
                ctx: parseFloat(this.formAdd.CTX),
                cqm: parseFloat(this.formAdd.CQM),
                dox: parseFloat(this.formAdd.DOX),
                fox: parseFloat(this.formAdd.FOX),
                ffc: parseFloat(this.formAdd.FFC),
                fos: parseFloat(this.formAdd.FOS),
                gen: parseFloat(this.formAdd.GEN),
                imp: parseFloat(this.formAdd.IMP),
                oqx: parseFloat(this.formAdd.OQX),
                neo: parseFloat(this.formAdd.NEO),
                sxt: parseFloat(this.formAdd.SXT),
                str: parseFloat(this.formAdd.STR),
                tet: parseFloat(this.formAdd.TET),
                mem: parseFloat(this.formAdd.MEM),
                operator: this.formAdd.man,
                micdetectiontype: this.formAdd.style,
                remark: this.formAdd.remark
            }
            this.$refs['formAdd'].validate((valid) => {
                if (valid) {
                    if( this.currentOpert){//新增
                        axios.post(that.URL+'/mic/negative/addition', param)
                        .then(function (res) {
                            if(res.data.state){
                                that.$message({
                                  type: 'success',
                                  message: res.data.value
                                });
                                that.dialogFormVisible = false
                                that.getList()
                            }else{
                                that.$message({
                                  type: 'error',
                                  message: res.data.value
                                });
                            }
                        })
                        .catch(function (error) {
                          console.error(error);
                        });
                        this.dialogFormVisible = false
                    }else{//修改
                        axios.post(that.URL+'/mic/negative/update', param)
                        .then(function (res) {
                            if(res.data.state){
                                that.$message({
                                  type: 'success',
                                  message: res.data.value
                                });
                                that.dialogFormVisible = false
                                that.getList()
                            }else{
                                that.$message({
                                  type: 'error',
                                  message: res.data.value
                                });
                            }
                        })
                        .catch(function (error) {
                          console.error(error);
                        });
                        this.dialogFormVisible = false
                    }
                } else {
                  console.log('error submit!!');
                  return false;
                }
            });
        },
        getList(){
            let that = this
            var param = {
                strainno : that.formSamInfo.ID,
                micalias : that.formSamInfo.INNO,
                page: that.currentPage,
                rows: that.pageSize,
            }
            for( var item of that.add_range){
                param[(item.R).toLowerCase()+'1'] = parseFloat(item.S)
                param[(item.R).toLowerCase()+'2'] = parseFloat(item.E)
            }
            // console.warn(param)
            that.loading2 = true

            //把药品查找范围的药品在table前面列显示
            if((that.add_range).length>=1){
                that.YinSeq = [];
                (that.add_range).forEach((range, i) => {
                    (that.YinSeq)[i] = range.R;
                });
                var tmp = [];
                for(var item of dataSet.yinDrug){
                    tmp.push(item);
                }
                (that.add_range).forEach((range, i) => {
                    tmp.forEach((val, j) => {
                        if(val == range.R){
                            tmp.splice(j,1)
                        }
                    })
                });
                tmp.forEach((val, j) => {
                    (that.YinSeq).push(val);
                });
            }else{
                that.YinSeq = dataSet.yinDrug
                // console.warn(dataSet.yinDrug)
            }
            // console.log(that.YinSeq)
            axios.post( that.URL + '/mic/negative/list', param)
                .then((res) => {
                    if(res.data.state){
                        that.tableData = []
                        that.totalPage = res.data.value.pages

                        for(var item of res.data.value.data){

                            that.tableData.push({
                                TB_id: item.strainno,
                                TB_inno: item.micalias,
                                AMK: item.amk,
                                AMP: item.amp,
                                APR: item.apr,
                                CAZ: item.caz,
                                CL: item.cl,
                                CHL: item.chl,
                                CIP: item.cip,
                                CTX: item.ctx,
                                CQM: item.cqm,
                                DOX: item.dox,
                                FOX: item.fox,
                                FFC: item.ffc,
                                FOS: item.fos,
                                GEN: item.gen,
                                IMP: item.imp,
                                OQX: item.oqx,
                                NEO: item.neo,
                                SXT: item.sxt,
                                STR: item.str,
                                TET: item.tet,
                                MEM: item.mem,
                                TB_man: item.operator,
                                TB_style: item.micdetectiontype,
                                TB_remark: item.remark,
                            })
                        }
                        that.loading2 = false
                    }
                })
                .catch((error) => {
                    console.error(error);
                })
        },
        addRange(){ //添加药品范围
            for(var item of this.add_range){
                if(this.formSamInfo.DRUGRANGE == item.R){
                    this.$message.error('该筛选已添加，无须重复添加！');
                    return false;
                }
            }
            if(dataRules.serDrugRule(this.formSamInfo.DRUGRANGE, this.formSamInfo.DRUGRANGE_S, this.formSamInfo.DRUGRANGE_E)){
                var that = this;
                this.add_range.push({
                    R:this.formSamInfo.DRUGRANGE,  S:this.formSamInfo.DRUGRANGE_S, E:this.formSamInfo.DRUGRANGE_E
                })
            }else{this.$message.error('输入不能为空，范围从小到大(0-2048)，且为数字！');}
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            if(formName == 'formSamInfo'){
                this.add_range = []
                this.formSamInfo.DRUGRANGE_S = "";
                this.formSamInfo.DRUGRANGE_E = "";
                // this.YinSeq = dataSet.yinDrug
                this.getList()
            }else if(formName == 'formAdd'){
                this.formAdd = {
                    id: '',
                    inno: '',
                    AMK: '',
                    AMP: '',
                    APR: '',
                    CAZ: '',
                    CL: '',
                    CHL: '',
                    CIP: '',
                    CTX: '',
                    CQM: '',
                    DOX: '',
                    FOX: '',
                    FFC: '',
                    FOS: '',
                    GEN: '',
                    IMP: '',
                    OQX: '',
                    NEO: '',
                    SXT: '',
                    STR: '',
                    TET: '',
                    MEM: '',
                    man: '',
                    style: '',
                    remark: '',
                }
                this.dialogFormVisible = false
            }
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
      }
  },
  created(){
      for(var item of dataSet.yinDrug){
          (this.YinDrugSet).push(item);
          (this.YinSeq).push(item);
      }
      this.getList();
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
    /*overflow: hidden;*/
}
/*表单输入组*/
.inputGp{
    margin-bottom: 30px;
}
.el-form-item{
    margin-bottom: 6px;
}
.el-input, .el-select, .el-date-editor{
    width: 160px !important;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-bottom: 30px;
}
.tableBox{
    border-radius: 4px !important;
}
.tableDIV{

}
.el-table{

}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 160px !important;
}
</style>



