<template>
  <div id="samSiteImpotDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="采样地信息"></my-location>
              <my-navigat activ="1-1"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                    <el-upload
                      :action="uploadURL"
                      :on-preview="handlePreview"
                      :on-remove="handleRemove"
                      :on-success="succeedUpload"
                      :on-error="failUpload"
                      :default-file-list="fileList">
                      <el-button size="small" type="primary">点击上传</el-button>
                      <div class="el-upload__tip" slot="tip">只能上传excel文件，excel的格式模板可点击下方按钮下载</div>
                    </el-upload>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button icon="d-arrow-left" @click="goBack">返回</el-button>
                    <el-button icon="caret-bottom" type="primary" @click="downLoadFile">下载导入模板</el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button type="primary" icon="edit" @click="onEdit" style="border-radius:4px;">修改</el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height = "600"
                    @select="deleteRow"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      fixed
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_CaiYang"
                      label="采样地名称"
                      fixed
                      width="200">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Province"
                      label="省份"
                      width="65">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Addres"
                      label="详细地址"
                      width="230">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Man"
                      label="联系人"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Tel"
                      label="联系电话"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_SiYang"
                      label="饲养方式"
                      width="82">
                    </el-table-column>
                    <el-table-column
                      prop="TB_GuiMo"
                      label="养殖规模"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_YModel"
                      label="养殖模式"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="BuildDate"
                      label="建厂时间"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Remark"
                      label="备注"
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_failReason"
                      label="失败原因"
                      fixed="right"
                      width="400">
                    </el-table-column>
                  </el-table>
              </div>
              <el-dialog title="采样地信息" v-model="dialogFormVisible">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="采样地名称" :label-width="formLabelWidth" prop="caiY">
                      <el-input v-model="formAdd.caiY" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="省份" :label-width="formLabelWidth" prop="provc">
                      <el-select  v-model="formAdd.provc" placeholder="选择省份">
                          <el-option :value="prvnc.name" v-for="prvnc in provc"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="详细地址" :label-width="formLabelWidth" prop="addres">
                      <el-input v-model="formAdd.addres" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="联系人" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="联系电话" :label-width="formLabelWidth" prop="tel">
                      <el-input v-model="formAdd.tel" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="饲养方式" :label-width="formLabelWidth" prop="siya">
                      <el-input v-model="formAdd.siya" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="养殖规模" :label-width="formLabelWidth" prop="guimo">
                      <el-input v-model="formAdd.guimo" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="养殖模式" :label-width="formLabelWidth" prop="moshi">
                      <el-select  v-model="formAdd.moshi" placeholder="选择养殖模式">
                          <el-option :value="moshi" v-for="moshi in yangModel"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="建厂时间" :label-width="formLabelWidth" prop="date1">
                        <el-date-picker type="date" placeholder="选择日期" v-model="formAdd.date1" :editable="false"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../../components/header'
import myLocation from './../../components/location'
import myNavigat from './../../components/navigat'
import axios from 'axios'
import dataSet from './../../publicAPI/dataSet'
import dataRules from './../../publicAPI/dataRules'
import config from './../../publicAPI/config'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data() {
      return {
          rootURL: config.rootURL,
          uploadURL: config.rootURL + '/farm/upload',
          tableData: [],
          multipleSelection: [],
          formAdd:{
              caiY: '',
              provc: '',
              man: '',
              tel: '',
              siya: '',
              guimo: '',
              moshi: '',
              date1: '',
              remark: '',
          },
          formAddRules:{
              caiY: [
                   { required: true, message: '请输入活动名称', trigger: 'blur' },
                   { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
              ],
              provc: [
                   { required: true, message: '请选择省份', trigger: 'change' },
              ],
              tel:[
                   { min: 1, max: 20, message: '长度在 1 到 20 个字符', trigger: 'blur' }
              ]
          },
          provc: dataSet.province,
          yangModel: dataSet.yangModel,
          switchSH: true,   //查询
          switchNV: true,   //导航
          loading2: true,    //表格加载中
          dialogFormVisible: false,
          formLabelWidth: '100px',
          cotDivWidth:{
              width: '1150px',
          },
          fileList: [
          ],
          selectRow: '',
      };
  },
  methods:{
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        deleteRow(selection, row){
            this.selectRow = row.TB_CaiYang
        },
        onEdit(){
            if(this.multipleSelection.length==1){
                this.dialogFormVisible = true
                this.formAdd = {
                    caiY: this.multipleSelection[0].TB_CaiYang,
                    provc: this.multipleSelection[0].TB_Province,
                    man: this.multipleSelection[0].TB_Man,
                    tel: this.multipleSelection[0].TB_Tel,
                    siya: this.multipleSelection[0].TB_SiYang,
                    guimo: this.multipleSelection[0].TB_GuiMo,
                    moshi: this.multipleSelection[0].TB_YModel,
                    date1: this.multipleSelection[0].BuildDate,
                    remark: this.multipleSelection[0].TB_Remark,
                }
            }
            // console.log(this.multipleSelection);
        },
        onConfirm(){
            let that = this
            this.$refs['formAdd'].validate((valid) => {
                  if (valid) {
                      var param = {
                          farmname: that.formAdd.caiY ,
                          farmprovince: that.formAdd.provc ,
                          farmlinkman: that.formAdd.man ,
                          farmphone: that.formAdd.tel ,
                          farmraiseway: that.formAdd.siya ,
                          farmraisescope: that.formAdd.guimo ,
                          farmraisetype: that.formAdd.moshi ,
                          farmbuilddate: dataRules.formatDate(that.formAdd.date1) ,
                          farmremarks: that.formAdd.remark ,
                      }
                      var str = JSON.stringify(param)
                      axios.post(that.rootURL+'/farm/addition', param)
                      .then(function (res) {
                          if(res.data.state){
                              that.$message({
                                type: 'success',
                                message: res.data.value
                              });
                              that.tableData.forEach((item, index) => {
                                //   console.log(item.TB_CaiYang+' --- '+that.selectRow)
                                  if(item.TB_CaiYang == that.selectRow){
                                      that.tableData.splice(index, 1);
                                  }
                              });
                          }else{
                              that.$message({
                                type: 'error',
                                message: res.data.value
                              });
                          }
                      })
                      .catch(function (error) {
                          console.error(error);
                      });
                      that.dialogFormVisible = false
                  } else {
                    console.log('error submit!!');
                    return false;
                  }
            });
        },
        resetForm(formName){
            this.$refs[formName].resetFields();
            this.formAdd = {
                caiY: '',
                provc: '',
                man: '',
                tel: '',
                siya: '',
                guimo: '',
                moshi: '',
                date1: '',
                remark: '',
            }
            this.dialogFormVisible = false
        },
        succeedUpload(response, file, fileList){
            let that = this;
            if(response.state){
                this.$message({
                  message: response.value,
                  duration: 5000,
                  type: 'success'
                });

            }else{
                if(typeof(response.value) != 'string'){
                    this.$message({
                      message: '成功导入【' + response.value.success + '】条，有【' + response.value.error + '】条导入失败！',
                      duration: 5000,
                      type: 'warning'
                    });
                    /*插入数据*/
                    that.tableData = [];
                    for( var item of response.value.data){
                        that.tableData.push({
                            TB_CaiYang: item.farmname,
                            TB_Province: item.farmprovince,
                            TB_Addres: item.farmaddress,
                            TB_Man: item.farmlinkman,
                            TB_Tel: item.farmphone,
                            TB_SiYang: item.farmraiseway,
                            TB_GuiMo: item.farmraisescope,
                            TB_YModel: item.farmraisetype,
                            BuildDate: item.farmbuilddate,
                            TB_Remark: item.farmremarks,
                            TB_failReason: item.errorMsg
                        })
                    }
                }else{
                    this.fileList = [];
                    this.$message({
                      message: response.value,
                      type: 'error'
                    });
                }
            }
        },
        failUpload(err, response, file){
            this.fileList = [];
            this.$message({
              message: err,
              type: 'error'
            });
        },
        goBack(){
            this.$router.push({ path: '/sampleSiteInfo' })
        },
        downLoadFile(){
            window.open('/static/uploads/farm.xls')
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
   },
   watch:{
       switchNV(val){
           if(val){
              this.cotDivWidth = {
                   width: '1150px',
              }
          }else{
              this.cotDivWidth = {
                   width: '100%',
              }
          }
      },
   }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-top: 20px;
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
/*对话框*/
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



