<!-- 菌株基本信息 | created by JanvierCheng -->
<template>
  <div id="strainBasicInfoDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="菌株基本信息"></my-location>
              <my-navigat activ="1-3"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="84px" :inline="true" class="demo-form-inline">
                      <el-form-item label="菌株编号" prop="ID">
                          <el-input v-model="formSamInfo.ID"></el-input>
                      </el-form-item>
                      <el-form-item label="样品编号" prop="YNO">
                          <el-input v-model="formSamInfo.YNO"></el-input>
                      </el-form-item>
                      <el-form-item label="内部编号" prop="INNO">
                          <el-input v-model="formSamInfo.INNO"></el-input>
                      </el-form-item>
                      <el-form-item label="菌属" prop="SBEBLONG">
                          <el-select  v-model="formSamInfo.SBEBLONG" multiple placeholder="选择菌属">
                              <el-option :value="item1" v-for="item1 in junshuSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="菌种" prop="STYPE">
                          <el-select  v-model="formSamInfo.STYPE" multiple placeholder="选择菌种">
                              <el-option :value="item2" v-for="item2 in junzhongSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="血清型" prop="SERUM">
                          <el-input v-model="formSamInfo.SERUM"></el-input>
                      </el-form-item>
                      <el-form-item label="MLST分型" prop="MLST">
                          <el-input v-model="formSamInfo.MLST"></el-input>
                      </el-form-item>
                      <el-form-item label="PLG分型" prop="PLG">
                          <el-input v-model="formSamInfo.PLG"></el-input>
                      </el-form-item>
                      <el-form-item label="菌种鉴定人" prop="SIDENTIFMAN">
                          <el-input v-model="formSamInfo.SIDENTIFMAN"></el-input>
                      </el-form-item>
                      <el-form-item label="细菌分型人" prop="SDEVIMAN">
                          <el-input v-model="formSamInfo.SDEVIMAN"></el-input>
                      </el-form-item>
                      <el-form-item label="保菌时间">
                          <el-date-picker type="date" placeholder="选择日期" v-model="formSamInfo.date1" :editable="false" style="width: 180px !important;"></el-date-picker>
                          -&nbsp;<el-date-picker type="date" placeholder="选择日期" v-model="formSamInfo.date2" :editable="false" style="width: 180px !important;"></el-date-picker>
                      </el-form-item>
                  </el-form>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="导航" off-text="导航" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="查询" off-text="查询" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                    <el-button-group>
                      <el-button type="primary" icon="plus" @click="onAdd">增加</el-button>
                      <el-button type="primary" icon="edit" @click="onEdit">修改</el-button>
                      <el-button type="primary" icon="delete" @click="onDelete">删除</el-button>
                    </el-button-group>
                    <el-button-group>
                      <router-link to="/strainBasicInfo/strainImpotNo"><el-button type="primary" icon="upload" style="border-radius: 5px 0 0 5px !important;">导入编号</el-button></router-link>
                      <router-link to="/strainBasicInfo/strainImpotFen"><el-button type="primary" icon="upload2">导入分型信息</el-button></router-link>
                      <el-button type="primary" icon="d-caret" @click="onExport">导出Excel</el-button>
                    </el-button-group>
                    <el-button-group>
                      <el-button type="primary" style="border-radius:4px;">打印标签</el-button>
                    </el-button-group>
                    <el-button-group>
                      <el-button icon="search" @click="getList">搜索</el-button>
                      <el-button icon="close" @click="resetForm('formSamInfo')">重置</el-button>
                    </el-button-group>
              </div>
              <!-- <div class="tableBox"> -->
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%;"
                    max-height="600"
                    v-loading.body="loading2"
                    element-loading-text="拼命加载中"
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_id"
                      fixed
                      label="菌株编号"
                      width="124">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      fixed
                      label="内部编号"
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_sampleid"
                      label="样品编号"
                      fixed
                      width="116">
                    </el-table-column>
                    <el-table-column
                      prop="TB_junshu"
                      label="菌属"
                      width="90">
                    </el-table-column>
                    <el-table-column
                      prop="TB_junzhong"
                      label="菌种"
                      width="142">
                    </el-table-column>
                    <el-table-column
                      prop="TB_savetime"
                      label="保存时间"
                      width="90">
                    </el-table-column>
                    <el-table-column
                      prop="TB_xueqing"
                      label="血清型"
                      width="60">
                    </el-table-column>
                    <el-table-column
                      prop="TB_mlst"
                      label="MLST分型"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_plg"
                      label="PLG分型"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_jiandingman"
                      label="菌种鉴定人"
                      width="160">
                    </el-table-column>
                    <el-table-column
                      prop="TB_fenxingman"
                      label="细菌分型人"
                      width="160">
                    </el-table-column>
                    <el-table-column
                      prop="TB_clocate"
                      label="采样地名称"
                      width="170">
                    </el-table-column>
                    <el-table-column
                      prop="TB_ylocate"
                      label="养殖场名称"
                      width="220">
                    </el-table-column>
                    <el-table-column
                      prop="TB_age"
                      label="日龄"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_source"
                      label="来源"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_part"
                      label="部位"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_caiyangman"
                      label="采样人员"
                      width="160">
                    </el-table-column>
                    <el-table-column
                      prop="TB_history"
                      label="动物用药史"
                      width="90">
                    </el-table-column>
                    <el-table-column
                      prop="TB_remark"
                      label="菌株备注">
                    </el-table-column>
                  </el-table>
              </div>
              <!-- </div> -->
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="菌株基本信息" v-model="dialogFormVisible" >
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="菌株编号" :label-width="formLabelWidth" prop="id" v-if="!currentOpert">
                      <el-input v-model="formAdd.id" auto-complete="off"  :disabled="!currentOpert"></el-input>
                    </el-form-item>
                    <el-form-item label="内部编号" :label-width="formLabelWidth" prop="inno" >
                      <el-input v-model="formAdd.inno" auto-complete="off" ></el-input>
                    </el-form-item>
                    <el-form-item label="样品编号" :label-width="formLabelWidth" prop="sampleid">
                        <el-input v-model="formAdd.sampleid" auto-complete="off"  :disabled="!currentOpert"></el-input>
                      <!-- <el-select  v-model="formAdd.sampleid" filterable  placeholder="选择样品编号">
                          <el-option :value="item" v-for="item in sampleNoSelect"></el-option>
                      </el-select> -->
                    </el-form-item>
                    <el-form-item label="菌属" :label-width="formLabelWidth" prop="junshu">
                      <el-select  v-model="formAdd.junshu"  placeholder="选择菌属" :disabled="!currentOpert">
                          <el-option :value="item"  v-for="item in junshuSelect"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="菌种" :label-width="formLabelWidth" prop="junzhong">
                      <el-select  v-model="formAdd.junzhong" placeholder="选择菌种" :disabled="formAdd.junshu == '' || !currentOpert">
                          <el-option :value="item"  v-for="item in junzhongspec"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="保存时间" :label-width="formLabelWidth" prop="time">
                      <el-date-picker type="date" placeholder="选择日期" v-model="formAdd.time" :editable="false" ></el-date-picker>
                    </el-form-item>
                    <el-form-item label="血清型" :label-width="formLabelWidth" prop="xuqing">
                      <el-input v-model="formAdd.xuqing" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="MLST分型" :label-width="formLabelWidth" prop="mlst">
                      <el-input v-model="formAdd.mlst" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="PLG分型" :label-width="formLabelWidth" prop="plg">
                      <el-input v-model="formAdd.plg" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="菌种鉴定人" :label-width="formLabelWidth" prop="jiandingMan">
                      <el-input v-model="formAdd.jiandingMan" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="细菌分型人" :label-width="formLabelWidth" prop="fenxingMan">
                      <el-input v-model="formAdd.fenxingMan" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="菌株备注" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')">取 消</el-button>
                    <el-button type="primary" @click="onConfirm">确 定</el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import dataSet from './../publicAPI/dataSet'
import axios from 'axios';
import config from './../publicAPI/config'
import dataRules from './../publicAPI/dataRules'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
  },
  data(){
      return{
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          dialogFormVisible: false,
          currentOpert: true, //true是新增，false是修改
          formSamInfo: {
                ID: '',
                YNO: '',
                SBEBLONG: [],
                STYPE: [],
                INNO: '',
                SERUM: '',
                MLST: '',
                PLG: '',
                SIDENTIFMAN: '',
                SDEVIMAN: '',
                date1: '',
                date2: ''
          },
          formAdd: {
              id: "",
              inno: "",
              sampleid: "",
              junshu: "",
              junzhong: "",
              time: "",
            //   timeEdit: "",
              xuqing: "",
              mlst: "",
              plg: "",
              jiandingMan: "",
              fenxingMan: "",
              remark: "",
          },
          formAddRules: {
              sampleid: [
                  { required: true, message: '请输入样品编号', trigger: 'blur' },
                  { min: 1, max: 13, message: '长度在 1 到 13 个字符', trigger: 'blur' }
              ],
              junshu: [
                  { required: true, message: '请选择菌属', trigger: 'change' },
              ],

          },
          switchSH: false,   //查询
          switchNV: true,   //导航
          loading2: false,
          currentPage: 1,
          pageSize: 100,
          totalPage: 0,
          formLabelWidth: '90px',
          cotDivWidth:{
              width: '1150px',
          },
          junshuSelect: [],
          junzhongSelect: [], //查询的---所有菌属
          junzhongspec: [], //新增先选菌属---部分菌属
        //   sampleNoSelect: []
      }
  },
  methods:{
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        onAdd(){
            this.dialogFormVisible = true
            this.currentOpert = true
        },
        onEdit(){
            if(this.multipleSelection.length==1){
                this.dialogFormVisible = true
                this.currentOpert = false
                this.formAdd = {
                    id: this.multipleSelection[0].TB_id,
                    inno: this.multipleSelection[0].TB_inno,
                    sampleid: this.multipleSelection[0].TB_sampleid,
                    junshu: this.multipleSelection[0].TB_junshu,
                    junzhong: this.multipleSelection[0].TB_junzhong,
                    time: this.multipleSelection[0].TB_savetime,
                    xuqing: this.multipleSelection[0].TB_xueqing,
                    mlst: this.multipleSelection[0].TB_mlst,
                    plg: this.multipleSelection[0].TB_plg,
                    jiandingMan: this.multipleSelection[0].TB_jiandingman,
                    fenxingMan: this.multipleSelection[0].TB_fenxingman,
                    remark: this.multipleSelection[0].TB_remark,
                }
            }
        },
        onDelete(){
            if(this.multipleSelection.length>0){
                let that = this;
                this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then(() => {
                    let str = "";
                    for(var item of this.multipleSelection){
                        str = str + item.TB_id + ','
                    }
                    axios.post(that.URL+'/strain/deletion', {
                       strainno: str
                    })
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.getList();
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                    });

                }).catch((error) => {
                    // this.$message.error(error);
                });
            }
        },
        onConfirm(){
            let that = this
            var param = {
                strainalias: this.formAdd.inno,
                sampleno: this.formAdd.sampleid,
                straincategory: this.formAdd.junshu,
                straintype: this.formAdd.junzhong,
                serotype: this.formAdd.xuqing,
                strainmlst: this.formAdd.mlst,
                strainplg: this.formAdd.plg,
                operator: this.formAdd.jiandingMan,
                strainparter: this.formAdd.fenxingMan,
                strainremarks: this.formAdd.remark,
                strainstoragedateStr: dataRules.formatDate(that.formAdd.time)
            }
            this.$refs['formAdd'].validate((valid) => {
                if (valid) {
                    if( this.currentOpert){//新增
                        console.log('样品信息新增：'+param)
                        axios.post(that.URL+'/strain/addition', param)
                        .then(function (res) {
                            if(res.data.state){
                                that.$message({
                                  type: 'success',
                                  message: res.data.value
                                });
                                that.dialogFormVisible = false
                                that.getList()
                            }else{
                                that.$message({
                                  type: 'error',
                                  message: res.data.value
                                });
                            }
                        })
                        .catch(function (error) {
                          console.error(error);
                        });
                        this.dialogFormVisible = false
                    }else{//修改
                        param.strainno = that.formAdd.id
                        axios.post(that.URL+'/strain/update', param)
                        .then(function (res) {
                            if(res.data.state){
                                that.$message({
                                  type: 'success',
                                  message: res.data.value
                                });
                                that.dialogFormVisible = false
                                that.getList()
                            }else{
                                that.$message({
                                  type: 'error',
                                  message: res.data.value
                                });
                            }
                        })
                        .catch(function (error) {
                          console.error(error);
                        });
                        this.dialogFormVisible = false
                    }
                } else {
                  console.log('error submit!!');
                  return false;
                }
            });
        },
        getList(){
            let that = this;
            this.loading2 = true
            var param = {
                strainno: this.formSamInfo.ID,
                sampleno: this.formSamInfo.YNO,
                straincategory: this.formSamInfo.SBEBLONG,
                straintype: this.formSamInfo.STYPE,
                strainalias: this.formSamInfo.INNO,
                serotype: this.formSamInfo.SERUM,
                strainmlst: this.formSamInfo.MLST,
                strainplg: this.formSamInfo.PLG,
                operator: this.formSamInfo.SIDENTIFMAN,
                strainparter: this.formSamInfo.SDEVIMAN,
                beginDate: this.formSamInfo.date1,
                endDate: this.formSamInfo.date2,
                rows: that.pageSize,
                sort: '',
                order: '',
                page: that.currentPage,
            }
            console.log(JSON.stringify(param))
            axios.post(that.URL+'/strain/list', param)
            .then(function (res) {
                that.tableData = [];
                that.totalPage = res.data.value.pages
                for( var item of res.data.value.data){
                    that.tableData.push({
                        TB_id: item.strainno,
                        TB_inno: item.strainalias,
                        TB_sampleid: item.sampleno,
                        TB_junshu: item.straincategory,
                        TB_junzhong: item.straintype,
                        TB_savetime: item.strainstoragedateStr,
                        TB_xueqing: item.serotype,
                        TB_mlst: item.strainmlst,
                        TB_plg: item.strainplg,
                        TB_jiandingman: item.operator,
                        TB_fenxingman: item.strainparter,
                        TB_clocate: item.farmName,
                        TB_ylocate: item.samplefarmaddr,
                        TB_age: item.sampleanimalage,
                        TB_source: item.samplesource,
                        TB_part: item.partName,
                        TB_caiyangman: item.samplecollector,
                        TB_history: item.samplemedicalhistory,
                        TB_remark: item.strainremarks,
                    })
                }
                that.loading2 = false;
            })
            .catch(function (error) {
              console.error(error);
            });
        },
        onExport(){
            let that = this;
            if((this.multipleSelection).length > 0){
                var strainno = '';
                for ( var item of this.multipleSelection){
                    strainno = strainno + item.TB_id + ','
                }
                console.log(strainno)
                axios.get(that.URL + '/strain/download?strainno=' + strainno)
                    .then((res) => {
                        window.open(that.URL + '/strain/download?strainno=' + strainno)
                    })
                    .catch((error) =>{
                        console.error(error);
                    })
            }else{
                this.$message.error('请选择要导出的行！');
            }

        },
        getSelect(){
            let that = this
            axios.all([axios.post(that.URL + '/strainToNotes/category/selection'), axios.post(that.URL + '/strainToNotes/strain/selection')])
                .then(axios.spread(function (junshu, junzhong) {
                    if(junshu.data.state){
                        that.junshuSelect = junshu.data.value
                    }
                    if(junzhong.data.state){
                        that.junzhongSelect = junzhong.data.value
                    }
                }))
        },
        getJunShuSelect(){
            let that = this
            var param = {
                straincategory: that.formAdd.junshu
            }
            var str = 'straincategory=' + that.formAdd.junshu
            axios.post(that.URL + '/strainToNotes/strain/selection', {
                straincategory: that.formAdd.junshu
            })
                .then((res) => {
                    if(res.data.state){
                        that.junzhongspec = res.data.value
                    }
                })
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            if(formName == 'formSamInfo'){
                this.formSamInfo.date1 = "";
                this.formSamInfo.date2 = "";
                this.getList();
            }else if(formName == 'formAdd'){
                this.formAdd =  {
                    id: "",
                    inno: "",
                    sampleid: "",
                    junshu: "",
                    junzhong: "",
                    time: "",
                    xuqing: "",
                    mlst: "",
                    plg: "",
                    jiandingMan: "",
                    fenxingMan: "",
                    remark: "",
                }
                this.dialogFormVisible = false
            }

        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`当前页: ${val}`);
        }
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
     },
     dialogFormVisible(){
         if(!this.dialogFormVisible){
             this.$refs['formAdd'].resetFields();
         }
     },
     'formAdd.junshu':function(_new, _old){
         if(_old!=""){
             this.formAdd.junzhong = "";
             this.getJunShuSelect()
         }else{
             this.getJunShuSelect()
         }
     }
  },
  created(){
      this.getList();
      this.getSelect();
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
    /*overflow: hidden;*/
}
/*表单输入组*/
.inputGp{
    margin-bottom: 20px;
}
.el-form-item{
    margin-bottom: 4px;
}
.el-input, .el-select, .el-date-editor{
    width: 210px !important;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/*操作按钮组*/
.operatBtnG{
    margin-bottom: 30px;
}
.tableBox{
    border-radius: 4px !important;
}
.tableDIV{

}
.el-table{

}
/*全局盒子控制*/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/*分页*/
.block{
    margin-top: 40px;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



