<template>
  <div id="addDrugRangeDIV">
      <span class="el-icon-circle-check" v-if="show"></span>
      <span v-if="show">已选择范围：</span>
      <span v-for="item in items">
          {{ item.S }} ≤ {{ item.R }} ≤ {{ item.E }}、
      </span>
  </div>
</template>

<script>

export default {
  components: {

  },
  props:[ "items" ],
  data(){
      return{
          show: false
      }
  },
  methods:{
  },
  watch:{
      items: function(){
          if(this.items.length!=0){
              this.show = true;
          }else{
              this.show = false;
          }
      }
  }
}
</script>

<style scoped>
#addDrugRangeDIV{
    margin-bottom: 2px;
    margin-top: 6px;
}
span{
    color: #5e6d82;
    font-size: 14px;
    font-weight: normal;
    margin-left:5px;
}
</style>



